<template>
    <div class="pathSettingDialog">
        <el-form :model="form" :rules="rules" label-width="120px" ref="actionSettingForm" :form="form" :macNoList="macNoList" :siteTypeList="siteTypeList">
            <el-row>
                <el-col :span="8">
                    <el-form-item label="设备编码" prop="macNo">
                        <el-select size="mini" v-model="form.macNo">
                            <el-option v-for="item in macNoList" :label="item.name" :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="动作号" prop="activeNo">
                        <el-input size="mini" v-model="form.activeNo"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="动作名" prop="activeName">
                        <el-input size="mini" v-model="form.activeName"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="x" prop="x">
                         <el-input size="mini" v-model="form.x"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="y" prop="y">
                        <el-input size="mini" v-model="form.y"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="z" prop="z">
                        <el-input size="mini" v-model="form.z"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="顺序号" prop="serial">
                         <el-input size="mini" v-model="form.serial"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="偏移量" prop="skewing">
                        <el-input size="mini" v-model="form.skewing"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            
        </el-form>
    </div>
</template>
<script>
export default {
    props: ["form", "macNoList", "siteTypeList"],

    data() {
        return {
            rules: {
                macNo: [
                    { required: true, message: '请选择设备编码', trigger: 'change' }
                ],
                activeNo: [
                    { required: true, message: '请输入动作号', trigger: 'blur' }
                ],
                activeName: [
                    { required: true, message: '请输入动作名', trigger: 'blur' }
                ],
                x: [
                    { required: true, message: '请输入x', trigger: 'blur' }
                ],
                y: [
                    { required: true, message: '请输入y', trigger: 'blur' }
                ],
                z: [
                    { required: true, message: '请输入z', trigger: 'blur' }
                ],
                serial: [
                    { required: true, message: '请输入顺序号', trigger: 'blur' }
                ],
            }
        }
    }
}
</script>
<style lang="less">
    .pathSettingDialog {

    }
</style>



