<template>
    <div class="communicationKey">
        <search :data="searchData" :config="searchConfig" @search="search"></search>
        <set-table :tableConfig="tableConfig" :tableData="tableData" :toolbarConfig="tableConfig.toolbarConfig" @showAdd="showAdd" @showEdit="showEdit" @remove="remove" @pageChange="pageChange" @addRoute="addRoute"></set-table>
        <!-- 设置物流设备弹框 -->
        <toast :config="toastData" @cancel="toastData.dialogVisible=false" @saveAdd="saveAdd" @savaAgain="savaAgain" @saveEdit="saveEdit">
            <set-dialog :form="toastData.form" :macNoList="searchConfig[0][0].childrens" :siteTypeList="toastData.siteTypeList" ref="comDialog"></set-dialog>
        </toast>
        <!-- 通讯键值说明弹框 -->
        <toast :config="singleToastData" @cancelSingle="singleToastData.dialogVisible=false" @keysaveAdd="keysaveAdd">
            <detail-settingDialog v-if="singleToastData.flag == 'T'" :tableData="singleToastData.tableData" :form="singleToastData.form" ref="detailSetDialog"></detail-settingDialog>
        </toast>
    </div>
</template>
<script>
import search from '../../components/search'
import toolbar from '../../components/toolbar'
import setTable from '../../components/table'
import toast from '../../components/toast'
import setDialog from './setDialog'
import detailSettingDialog from './detailSettingDialog'
import {postWay,getWay,postJsonWay} from './interface.js'
export default {
    data() {
        return {
            /**搜索框配置 */
            searchData: {},
            searchConfig: [
                [
                    {label: "设备编码", field: "macNo", type: "select", childrens: []},
                    {label: "通讯键", field: "keyType", type: "input"}
                    
                ]
            ],

            /**表格配置 */
            tableConfig: {
                toolbarConfig: [
                    {icon: "", disabled: false, method: "showAdd", name: "新增"},
                    {icon: "", disabled: true, method: "showEdit", name: "编辑"},
                    {icon: "", disabled: true, method: "remove", name: "删除"}
                ],
                height: "430",
                isHigh: false,
                isSelection: true,
                align: "center",
                isOrder: false,
                currentSelectArr: [],
                currentClickRow: {},
                colConfig: [
                    {
                        fixed: false,
                        field: "macNo",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "设备编码",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "keyType",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "通讯键",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "directionType",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "上/下行标识",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "yieldType",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "产量标识",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "操作",
                        type: "btnsText",
                        btnData: [
                            {name: '通讯键值说明', method: 'addRoute'}
                        ]
                    }
                ],
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0
            },

            tableData: [],

            /**弹出框配置 */
            toastData: {
                title:'设置设备通讯建',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'980px',//弹窗宽
                btnData:[{
                    name:'保存',//按钮名
                    method:'saveAdd',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'保存并继续',
                    method:'savaAgain',
                    bgColor:'dialog-blue',
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }],

                form: {
                    macNo: '',
                    keyType: '',
                    direction: '',
                    isYield: ''
                }
            },

            singleToastData: {
                title:'通讯键值说明',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'980px',//弹窗宽
                btnData:[{
                    name:'保存',//按钮名
                    method:'keysaveAdd',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'关闭',
                    method:'cancelSingle',
                    bgColor:'dialog-normal',
                    isShow: true
                }],

                form: {
                    macNo: '',
                    keyType: '',
                    isYield: '',
                    direction: '',
                },

                tableData: [],

                flag: 'F'
            },
        }
    },

    components: {
      search,
      toolbar,
      setTable,
      toast,
      setDialog,
      detailSettingDialog
    },

    mounted() {
        this.selectList();

        /**设备编码下拉查询 */
        getWay({},this,'/sitSet/optionDeviceNo').then(data=>{
            data.forEach(v=> {
                v.name = v.diviceNo;
                v.value = v.diviceNo;
            }) 
            this.searchConfig[0][0].childrens = data;
        })
    },

    methods: {
        /**分页查询 */
        selectList() {
            var obj = {
                pageNo: this.tableConfig.pageNo,
                pageSize: this.tableConfig.pageSize,
                macNo: this.searchData.macNo,
                keyType: this.searchData.keyType
            };
            postWay(obj,this,'/commuKeySet/page').then(data=>{
                this.tableData = data.items;
                this.tableConfig.total = data.numRows;
            })
        },

        /**按搜索条件查询 */
        search() {
            this.tableConfig.pageNo = 1;
            this.tableConfig.pageSize = 20;
            this.selectList();
        },
        
        /**分页条页码、条数改变 */
        pageChange() {
            this.selectList();
        },

        /**出现新增弹框 */
        showAdd() {
            this.toastData.dialogVisible = true;
            if(this.$refs.comDialog && this.$refs.comDialog.$refs.comForm) {
                this.$refs.comDialog.$refs.comForm.resetFields();
            }
            this.toastData.btnData = [
                {
                    name:'保存',//按钮名
                    method:'saveAdd',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'保存并继续',
                    method:'savaAgain',
                    bgColor:'dialog-blue',
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }
            ];
            for(var key in this.toastData.form) {
                this.toastData.form[key] = "";
            }
        },

        /**保存新增 */
        saveAdd() {
            this.$refs.comDialog.$refs.comForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        macNo: this.toastData.form.macNo,
                        keyType: this.toastData.form.keyType,
                        direction: this.toastData.form.direction,
                        isYield: this.toastData.form.isYield,
                    };
                    postWay(obj,this,'/commuKeySet/addOrUpdate').then(data=>{
                        this.toastData.dialogVisible = false;
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
            
        },

        /**保存新增并继续 */
        savaAgain() {
            this.$refs.comDialog.$refs.comForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        macNo: this.toastData.form.macNo,
                        keyType: this.toastData.form.keyType,
                        direction: this.toastData.form.direction,
                        isYield: this.toastData.form.isYield,
                    };
                    postWay(obj,this,'/commuKeySet/addOrUpdate').then(data=>{
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        });
                        this.search();
                        for(var key in this.toastData.form) {
                            this.toastData.form[key] = "";
                        }
                        this.$nextTick(() => {
                            this.$refs.comDialog.$refs.comForm.resetFields();
                        })
                    })
                }else {
                    return false;
                }
            })
            
        },

        /**出现编辑弹框 */
        showEdit() {
            this.toastData.dialogVisible = true;
            this.toastData.btnData = [
                {
                    name:'保存',//按钮名
                    method:'saveEdit',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }
            ];
            for(var key in this.toastData.form) {
                this.toastData.form[key] = this.tableConfig.currentSelectArr[0][key];
            }
        },

        /**保存编辑 */
        saveEdit() {
            this.$refs.comDialog.$refs.comForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        id: this.tableConfig.currentSelectArr[0].id,
                        macNo: this.toastData.form.macNo,
                        keyType: this.toastData.form.keyType,
                        direction: this.toastData.form.direction,
                        isYield: this.toastData.form.isYield,
                    };
                    postWay(obj,this,'/commuKeySet/addOrUpdate').then(data=>{
                        this.toastData.dialogVisible = false;
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
            
        },

        /**删除 */
        remove() {
            this.$confirm("此操作将永久删除该节点设置，是否继续？", "提示", {
                confimButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                var arr = [];
                this.tableConfig.currentSelectArr.forEach(v => {
                    arr.push(v.id);
                });
                var obj = {ids: arr.join(',')};
                postWay(obj,this,'/commuKeySet/delete').then(data=>{
                    this.$message({
                        type: 'success',
                        message: '删除成功'
                    })
                    this.search();
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                })
            })
        },

        /**添加单路径 */
        addRoute(val) {
            this.singleToastData.dialogVisible = true;
            this.tableConfig.currentClickRow = val;
            for(var key in this.singleToastData.form) {
                this.singleToastData.form[key] = val[key];
            }
            var obj = {
                id: val.id
            }
            postWay(obj,this,'/commuKeySet/queryDetail').then(data=>{
                this.singleToastData.flag = 'T';
                this.singleToastData.tableData = data.commuKeyvalueSets?data.commuKeyvalueSets:[];
            })
        },

        /**通讯键值说明保存 */
        keysaveAdd() {
            if(this.singleToastData.tableData.length < 1) {
                this.$message({
                    type: "warning",
                    message: "请输入键值"
                })
            }else {
                this.singleToastData.tableData.forEach((v,i) => {
                    v.keyId = this.tableConfig.currentClickRow.id;
                })
                var obj = this.singleToastData.tableData
                postJsonWay(obj,this,'/commuKeyvalueSet/addOrUpdate').then(data=>{
                    this.singleToastData.dialogVisible = false;
                    this.$message({
                        type: 'success',
                        message: '保存成功'
                    })
                    this.search();
                })
            }
            
        }
    },

    watch: {
        "tableConfig.currentSelectArr": function(val) {
            if(val.length == 0) {
                this.tableConfig.toolbarConfig[1].disabled = true;
                this.tableConfig.toolbarConfig[2].disabled = true;
            }else if(val.length == 1) {
                this.tableConfig.toolbarConfig[1].disabled = false;
                this.tableConfig.toolbarConfig[2].disabled = false;
            }else if(val.length > 1) {
                this.tableConfig.toolbarConfig[1].disabled = true;
                this.tableConfig.toolbarConfig[2].disabled = false;
            }
        },

        "singleToastData.dialogVisible": function(val) {
            if(!val) {
                this.singleToastData.flag = 'F';
            }
        }
    }
}
</script>
<style lang="less">
    .communicationKey {
        position: absolute;
        top: 120px;
        bottom: 20px;
        left: 20px;
        right: 20px;
        background: #fff;
        padding: 20px;
        overflow: auto;
        box-sizing: border-box;
        .myTable {
        }
    }
</style>



