import Vue from 'vue'
import Router from 'vue-router'
// import Index from '../page/index/index.vue'
import Test from '../page/index/index.vue'
import task from '../page/task/index.vue'
import home from '../page/home/index.vue'
import lock from '../page/lock/index.vue'
import define from '../page/define/index.vue'
import scheduling from '../page/scheduling/index.vue'
import communicationKey from '../page/communicationKey/index'
Vue.use(Router)
const router= new Router({
  // mode:'history',
  scrollBehavior:()=>({
    y:0
  }),
  routes:[{
    path: '/index',
    component:Test,
    children:[{
      path:'/task/index',component:task
    },{
      path:'/home/index',component:home
    },{
      path:'/define/index',component:define
    },{
      path:'/scheduling/index',component:scheduling
    },{
      path:'/communicationKey/index',component:communicationKey
    }]
  },{
    path: '/lock',
    component:lock
  }]
})

export default router


