<template>
    <div class="pathSettingDialog">
        <el-form :model="form" :rules="rules" label-width="120px" ref="equipmentSettingForm" :form="form" :macNoList="macNoList" :siteTypeList="siteTypeList" size="mini">
            <el-row>
                <el-col :span="8">
                    <el-form-item label="设备编码" prop="diviceNo">
                        <el-input size="mini" v-model.trim="form.diviceNo"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="机联网编码" prop="catNo" size="mini">
                        <el-select v-model="form.catNo" >
                          <el-option v-for="(item,index) in catNoList" :key="index" :label="item.commcode" :value="item.commcode"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="品牌" prop="brand">
                        <el-input size="mini" v-model.trim="form.brand"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="出厂年月" prop="ym">
                        <el-date-picker value-format="yyyy-MM-dd" size="mini" v-model="form.ym" type="date" style="width:99%"></el-date-picker>
                    </el-form-item>
                </el-col>
                 <el-col :span="8">
                    <el-form-item label="生产厂家" prop="factory">
                         <el-input size="mini" v-model.trim="form.factory"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="库区" prop="storeAreaId">
                        <el-input size="mini" v-model.trim="form.storeAreaId"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <!-- <el-row>
                <el-col :span="8">
                    <el-form-item label="max-x" prop="maxX">
                         <el-input size="mini" v-model.trim="form.maxX"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="max-y" prop="maxY">
                        <el-input size="mini" v-model.trim="form.maxY"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="max-z" prop="maxZ">
                        <el-input size="mini" v-model.trim="form.maxZ"></el-input>
                    </el-form-item>
                </el-col>
            </el-row> -->
        </el-form>
    </div>
</template>
<script>
import {optionNetworkNo} from './interface.js'
export default {
    props: ["form", "macNoList", "siteTypeList"],

    data() {
        var validateDiviceNo = (rule,value,callback) => {
            var reg = /^[a-zA-Z0-9]+$/;
            if(reg.test(value)) {
                callback();
            }else {
                callback(new Error('请输入英文或数字'));
            }
        }
        return {
            catNoList:[],
            rules: {
                diviceNo: [
                    { required: true, message: '请输入设备编码', trigger: 'blur' },
                    { max: 30, message: '最大长度为30个字符', trigger: 'blur' },
                    { validator: validateDiviceNo, trigger: 'blur' }
                ],
                // communicationType: [
                //     { required: true, message: '请输入通讯方式', trigger: 'blur' },
                //     { max: 10, message: '最大长度为10个字符', trigger: 'blur' }
                // ],
                // ip: [
                //     { required: true, message: '请输入ip地址', trigger: 'blur' },
                // ],
                catNo: [
                    { required: true, message: '请输入机器猫编码', trigger: 'blur' },
                ],
                // macAddr: [
                //     { required: true, message: '请输入mac地址', trigger: 'blur' },
                // ],
                // brand: [
                //     { required: true, message: '请输入品牌', trigger: 'blur' },
                // ],
                // plc: [
                //     { required: true, message: '请输入PLC型号', trigger: 'blur' },
                // ],
                // ym: [
                //     { required: true, message: '请输入出厂年月', trigger: 'blur' },
                // ],
                // sfVersion: [
                //     { required: true, message: '请输入固件版本号', trigger: 'blur' },
                // ],
                // maxX: [
                //     { required: true, message: '请输入max-x', trigger: 'blur' },
                // ],
                // maxY: [
                //     { required: true, message: '请输入max-y', trigger: 'blur' },
                // ],
                // maxZ: [
                //     { required: true, message: '请输入max-z', trigger: 'blur' },
                // ],
                // factory: [
                //     { required: true, message: '请输入生产厂家', trigger: 'blur' },
                // ],
                // storeAreaId: [
                //     { required: true, message: '请输入库区', trigger: 'blur' },
                // ],
            }
        }
    },
    mounted(){
        optionNetworkNo().then(res=>{
            console.log(res,'+++')
            this.catNoList=res
        })
    }
}
</script>
<style lang="less">
    .pathSettingDialog {

    }
</style>



