<template>
    <div class="pathSettingDialog">
        <el-form :model="form" label-width="120px" ref="detailSetForm" :form="form" :macNoList="macNoList" :siteTypeList="siteTypeList">
            <el-row>
                <el-col :span="8">
                    <el-form-item label="设备编码：" prop="macNo">
                        {{form.macNo}}
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="通讯键：" prop="keyType">
                        {{form.keyType}}
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="产量标识：" prop="isYield">
                        {{form.isYield}}
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="上/下行标识：" prop="direction">
                        {{form.direction}}
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <el-button size="mini" @click="add">新增</el-button>
        <el-button size="mini" @click="remove" :disabled="selectRow.length==0">删除</el-button>
        <el-table :data="tableData" style="width:100%" @select="select" @select-all="select">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="keyValue" label="通讯键值">
                <template slot-scope="scope">
                    <el-input size="mini" v-model="scope.row.keyValue" @blur="keyValueChange(scope.row.keyValue,scope.$index)"></el-input>
                </template>
            </el-table-column>
            <el-table-column prop="keyDesc" label="键值说明">
                <template slot-scope="scope">
                    <el-input size="mini" v-model="scope.row.keyDesc"></el-input>
                </template>
            </el-table-column>
            <el-table-column prop="remark" label="备注">
                <template slot-scope="scope">
                    <el-input size="mini" v-model="scope.row.remark"></el-input>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
export default {
    props: ["form", "macNoList", "siteTypeList", "tableData"],

    data() {
        return {
            keyTableData: this.tableData,
            selectRow: []
        }
    },

    methods: {
        add() {
            this.keyTableData.push({});
        },

        select(val,row) {
            this.selectRow = val;
        },

        remove() {
            this.selectRow.forEach(val => {
                this.keyTableData.forEach((v,i) => {
                    if(v.keyValue == val.keyValue) {
                        this.keyTableData.splice(i,1);
                    }
                })
            })
        },

        keyValueChange(val,index) {
            this.keyTableData.forEach((v,i) => {
                if(i != index && val == v.keyValue) {
                    this.$message({
                        type: 'warning',
                        message: '键值已存在，请重新输入'
                    })
                    this.keyTableData[index].keyValue = "";
                }
            })
        }
    }
}
</script>
<style lang="less">
    .pathSettingDialog {

    }
</style>



