<template>
    <div class="scheduling">
        <div class="tabs">
            <ul>
                <li :class="{'is-active':current==item}" v-for="item in tabs" :key="item" @click="current=item">{{item}}</li>
            </ul>
        </div>
        <div class="content">
            <transition name="fade-transverse">
                <!-- <control-page v-if="current=='物流调度'"></control-page> -->
                <!-- <debug v-if="current=='设备调试'"></debug> -->
                <node-settings v-if="current=='设备节点设置'"></node-settings>
                <!-- <path-settings v-if="current=='设备单路径设置'"></path-settings>
                <paths-settings v-if="current=='设备路径集设置'"></paths-settings>
                <action-settings v-if="current=='设备动作设置'"></action-settings> -->
            </transition>
        </div>
    </div>
</template>
<script>
import controlPage from './controlPage.vue'
import debug from './debug.vue'
import nodeSettings from './nodeSettings'
import pathSettings from './pathSettings'
import pathsSettings from './pathsSettings'
import actionSettings from './actionSettings'
// import {webSocket} from '../../api/webSockt.js'
export default {
    data(){
        return{
            current:'设备节点设置',
            tabs:['设备节点设置'],
            websocket:null
        }
    },
    components:{
        controlPage,
        debug,
        nodeSettings,
        pathSettings,
        pathsSettings,
        actionSettings
    },
    mounted(){
        this.websocket = new WebSocket(window.sessionStorage.socketUrl);
        this.websocket.onmessage=(res)=>{
            console.log(res.data)
        }
        this.websocket.onerror=(err)=>{
            console.log(err)
        }
    },
    destroyed(){
        if(this.websocket)
            this.websocket.onclose
    }
}
</script>
<style lang="less" >
    .fade-transverse-leave-active,
    .fade-transverse-enter-active {
        transition: all .5s;
    }

    .fade-transverse-enter {
        opacity: 0;
        transform: translateX(-30px);
    }

    .fade-transverse-leave-to {
        opacity: 0;
        transform: translateX(30px);
    }
    .scheduling{
        display: flex;
        flex-direction: column;
        // background: #fff;
        height: 100%;
        width: 100%;
        overflow: hidden;
        .tabs{
            width: 100%;
            border-bottom: 1px solid rgba(229,229,229,1);
            height: 50px;
            ul{
                &::after{
                    content: '';
                    display: block;
                    clear: both;
                }
                li{
                    float: left;
                    padding: 10px 40px 5px;
                    font-size:18px;
                    font-family:MicrosoftYaHei;
                    font-weight:400;
                    color:rgba(51,51,51,1);
                    line-height:36px;
                    position: relative;
                    cursor: pointer;
                    &.is-active::after{
                        content: '';
                        display:block;
                        width: 36px;
                        height: 2px;
                        position: absolute;
                        left: 50%;
                        bottom: 0px;
                        background: #2A93FF;
                        transform: translate(-50%,0);
                        animation: show .2s ease-in-out;
                    }
                }
            }
        }
        .content{
            flex:1;
            overflow: hidden;
        }
    }
    @keyframes show {
        from{
            width:0px;
        }
        to{
            width: 36px;
        }
    }
</style>




