<template>
    <div class="addpathSettingDialog">
        <div class="left-content">
            <p>已选择的单路径</p>
            <ul>
                <li v-for="(item,index) in comchoseList">
                    路径{{index+1}}： {{item.activeName}}
                </li>
            </ul>
        </div>
        <div class="right-content">
            <p>该设备下的单路径</p>
            <el-input class="search" size="mini" placeholder="搜索单路径"></el-input>
            <el-checkbox-group v-model="allSinglePathSets">
                <el-checkbox v-for="(item,index) in comrouteList" :label="item.id" :disabled="item.disabled">{{item.activeName}}</el-checkbox>
            </el-checkbox-group>
        </div>
    </div>
</template>
<script>
export default {
    props: ["choseList","routeList"],

    data() {
        return {
            allSinglePathSets: [],
            comchoseList: this.choseList,
            comrouteList: this.routeList
        }
    },

    mounted() {
        // this.comchoseList = this.choseList;
        // this.comrouteList = this.routeList;
        this.init();
    },

    methods: {
        init() {
            this.allSinglePathSets = [];
            for(var i = 0, len =this.comchoseList.length; i < len; i++) {
                this.allSinglePathSets.push(this.comchoseList[i].id);
            }
        }
    },

    watch: {
        "allSinglePathSets": function(val) {
            this.comchoseList = [];
            for(var i = 0, length = this.comrouteList.length; i < length; i++) {
                for(var k = 0, len = val.length; k < len; k++) {
                    if(val[k] == this.comrouteList[i].id) {
                        this.comchoseList.push(this.comrouteList[i])
                    }
                }
            }
            this.$emit("listChange",val)
        },

        "comchoseList": function(val) {
            if(val.length == 0) {
                this.comrouteList.forEach((v) => {
                    v.disabled = false;
                })
            }else {
                var mapObj = new Map();
                this.comchoseList.forEach((value) => {
                    mapObj.set(value.activeName,'true');
                })
                this.comrouteList.forEach((v) => {
                    if(mapObj.has(v.activeName)) {
                        v.disabled = false;
                    }else {
                        try {
                            this.comchoseList.forEach((value) => {
                                v.disabled = true;
                                if(value.activeName.split('->')[1] == v.activeName.split('->')[0]) {
                                    v.disabled = false;
                                    throw new Error('end');
                                }
                            })
                        }catch(e) {
                            if(e.message != 'end') {
                                throw e
                            }
                        }
                    }
                })
            }
        }
    }
}
</script>
<style lang="less">
    .addpathSettingDialog {
        overflow: auto;

        .left-content {
            float: left;
            width: 146px;
            border-right: 1px solid #E5E5E5;
            height: 300px;
            overflow: auto;

            p {
                margin-bottom: 15px;
            }

            ul {
                li {
                    padding: 5px 0;
                }
            }

            .el-icon-delete {
                cursor: pointer;
                float: right;
                margin-right: 10px;
                line-height: 18px;
            }
        }

        .right-content {
            float: left;
            width: 730px;
            padding: 0 20px;

            p {
                margin-bottom: 10px;
            }

            .search {
                margin-bottom: 10px;
            }

            .el-checkbox {
                width: 100px;
                margin-right: 20px;
                margin-bottom: 10px;
            }

            .el-checkbox+.el-checkbox {
                margin-left: 0;
            }
        }
    }
</style>



