<template>
    <div class="define">
        <search :data="searchData" :config="searchConfig" @search="search"></search>
        <set-table :tableConfig="tableConfig" :tableData="tableData" :toolbarConfig="tableConfig.toolbarConfig" @showAdd="showAdd" @showEdit="showEdit" @remove="remove" @pageChange="pageChange" @addRoute="addRoute"></set-table>
        <!-- 设置物流设备弹框 -->
        <toast :config="toastData" @cancel="toastData.dialogVisible=false" @saveAdd="saveAdd" @savaAgain="savaAgain" @saveEdit="saveEdit">
            <equipment-settingDialog :form="toastData.form" :macNoList="toastData.mac_NoList" :siteTypeList="toastData.siteTypeList" ref="defineDialog"></equipment-settingDialog>
        </toast>
        <!-- 查看详情弹框 -->
        <toast :config="singleToastData" @cancelSingle="singleToastData.dialogVisible=false">
            <detail-settingDialog :form="singleToastData.form" ref="nodsettingDialog"></detail-settingDialog>
        </toast>
    </div>
</template>
<script>
import search from '../../components/search'
import toolbar from '../../components/toolbar'
import setTable from '../../components/table'
import toast from '../../components/toast'
import equipmentSettingDialog from './equipmentSettingDialog'
import detailSettingDialog from './detailSettingDialog'
import {postWay,getWay} from './interface.js'
export default {
    data() {
        return {
            /**搜索框配置 */
            searchData: {},
            searchConfig: [
                [
                    {label: "设备编码", field: "diviceNo", type: "select", childrens: []}
                ]
            ],

            /**表格配置 */
            tableConfig: {
                toolbarConfig: [
                    {icon: "", disabled: false, method: "showAdd", name: "新增"},
                    {icon: "", disabled: true, method: "showEdit", name: "编辑"},
                    {icon: "", disabled: true, method: "remove", name: "删除"}
                ],
                height: "430",
                isHigh: false,
                isSelection: true,
                align: "center",
                isOrder: false,
                currentSelectArr: [],
                colConfig: [
                    {
                        fixed: false,
                        field: "diviceNo",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "设备编码",
                        type: "text",
                    },
                    // {
                    //     fixed: false,
                    //     field: "communicationType",
                    //     width: "",
                    //     isSort: false,
                    //     isResize: "",
                    //     noTooltip: "通讯方式",
                    //     type: "text",
                    // },
                    // {
                    //     fixed: false,
                    //     field: "ip",
                    //     width: "",
                    //     isSort: false,
                    //     isResize: "",
                    //     noTooltip: "ip地址",
                    //     type: "text",
                    // },
                    {
                        fixed: false,
                        field: "catNo",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "设备机联网编码",
                        type: "text",
                    },
                    // {
                    //     fixed: false,
                    //     field: "macAddr",
                    //     width: "",
                    //     isSort: false,
                    //     isResize: "",
                    //     noTooltip: "MAC地址",
                    //     type: "text",
                    // },
                    {
                        fixed: false,
                        field: "brand",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "品牌",
                        type: "text",
                    },
                    // {
                    //     fixed: false,
                    //     field: "plc",
                    //     width: "",
                    //     isSort: false,
                    //     isResize: "",
                    //     noTooltip: "PLC型号",
                    //     type: "text",
                    // },
                    // {
                    //     fixed: false,
                    //     field: "maxX",
                    //     width: "",
                    //     isSort: false,
                    //     isResize: "",
                    //     noTooltip: "max-x",
                    //     type: "text",
                    // },
                    // {
                    //     fixed: false,
                    //     field: "maxY",
                    //     width: "",
                    //     isSort: false,
                    //     isResize: "",
                    //     noTooltip: "max-y",
                    //     type: "text",
                    // },
                    // {
                    //     fixed: false,
                    //     field: "maxZ",
                    //     width: "",
                    //     isSort: false,
                    //     isResize: "",
                    //     noTooltip: "max-z",
                    //     type: "text",
                    // },
                    {
                        fixed: false,
                        field: "storeAreaId",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "库区",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "操作",
                        type: "btnsText",
                        btnData: [
                            {name: '查看详情', method: 'addRoute'}
                        ]
                    }
                ],
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0
            },

            tableData: [],

            /**弹出框配置 */
            toastData: {
                title:'设置物流设备',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'980px',//弹窗宽
                btnData:[{
                    name:'保存',//按钮名
                    method:'saveAdd',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'保存并继续',
                    method:'savaAgain',
                    bgColor:'dialog-blue',
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }],

                form: {
                    diviceNo: '',
                    communicationType: '',
                    ip: '',
                    catNo: '',
                    macAddr: '',
                    brand: '',
                    plc: '',
                    ym: '',
                    sfVersion: '',
                    maxX: '',
                    maxY: '',
                    maxZ: '',
                    factory: '',
                    storeAreaId: ''
                },

                mac_NoList: [],
                siteTypeList: []
            },

            singleToastData: {
                title:'查看详情',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'980px',//弹窗宽
                btnData:[{
                    name:'关闭',
                    method:'cancelSingle',
                    bgColor:'dialog-normal',
                    isShow: true
                }],

                form: {
                    diviceNo: '',
                    communicationType: '',
                    ip: '',
                    catNo: '',
                    macAddr: '',
                    brand: '',
                    plc: '',
                    ym: '',
                    sfVersion: '',
                    maxX: '',
                    maxY: '',
                    maxZ: '',
                    factory: '',
                    storeAreaId: ''
                },
            },
        }
    },

    components: {
      search,
      toolbar,
      setTable,
      toast,
      equipmentSettingDialog,
      detailSettingDialog
    },

    mounted() {
        this.selectList();

        /**设备编码下拉查询 */
        getWay({},this,'/sitSet/optionDeviceNo').then(data=>{
            data.forEach(v=> {
                v.name = v.diviceNo;
                v.value = v.diviceNo;
            }) 
            this.searchConfig[0][0].childrens = data;
        })
    },

    methods: {
        /**分页查询 */
        selectList() {
            var obj = {
                pageNo: this.tableConfig.pageNo,
                pageSize: this.tableConfig.pageSize,
                diviceNo: this.searchData.diviceNo
            };
            postWay(obj,this,'/deviceDefine/page').then(data=>{
                this.tableData = data.items;
                this.tableConfig.total = data.numRows;
            })
        },

        /**按搜索条件查询 */
        search() {
            this.tableConfig.pageNo = 1;
            this.tableConfig.pageSize = 20;
            this.selectList();
        },
        
        /**分页条页码、条数改变 */
        pageChange() {
            this.selectList();
        },

        /**出现新增弹框 */
        showAdd() {
            this.toastData.dialogVisible = true;
            if(this.$refs.defineDialog && this.$refs.defineDialog.$refs.equipmentSettingForm) {
                this.$refs.defineDialog.$refs.equipmentSettingForm.resetFields();
            }
            this.toastData.btnData = [
                {
                    name:'保存',//按钮名
                    method:'saveAdd',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'保存并继续',
                    method:'savaAgain',
                    bgColor:'dialog-blue',
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }
            ];
            for(var key in this.toastData.form) {
                this.toastData.form[key] = "";
            }
        },

        /**保存新增 */
        saveAdd() {
            this.$refs.defineDialog.$refs.equipmentSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        diviceNo: this.toastData.form.diviceNo,
                        communicationType: this.toastData.form.communicationType,
                        ip: this.toastData.form.ip,
                        catNo: this.toastData.form.catNo,
                        macAddr: this.toastData.form.macAddr,
                        brand: this.toastData.form.brand,
                        plc: this.toastData.form.plc,
                        ym: this.toastData.form.ym,
                        sfVersion: this.toastData.form.sfVersion,
                        maxX: this.toastData.form.maxX,
                        maxY: this.toastData.form.maxY,
                        maxZ: this.toastData.form.maxZ,
                        factory: this.toastData.form.factory,
                        storeAreaId: this.toastData.form.storeAreaId,
                    };
                    postWay(obj,this,'/deviceDefine/addOrUpdate').then(data=>{
                        this.toastData.dialogVisible = false;
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
            
        },

        /**保存新增并继续 */
        savaAgain() {
            this.$refs.defineDialog.$refs.equipmentSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        diviceNo: this.toastData.form.diviceNo,
                        communicationType: this.toastData.form.communicationType,
                        ip: this.toastData.form.ip,
                        catNo: this.toastData.form.catNo,
                        macAddr: this.toastData.form.macAddr,
                        brand: this.toastData.form.brand,
                        plc: this.toastData.form.plc,
                        ym: this.toastData.form.ym,
                        sfVersion: this.toastData.form.sfVersion,
                        maxX: this.toastData.form.maxX,
                        maxY: this.toastData.form.maxY,
                        maxZ: this.toastData.form.maxZ,
                        factory: this.toastData.form.factory,
                        storeAreaId: this.toastData.form.storeAreaId,
                    };
                    postWay(obj,this,'/deviceDefine/addOrUpdate').then(data=>{
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        });
                        this.search();
                        for(var key in this.toastData.form) {
                            this.toastData.form[key] = "";
                        }
                        this.$nextTick(() => {
                            this.$refs.defineDialog.$refs.equipmentSettingForm.resetFields();
                        })
                    })
                }else {
                    return false;
                }
            })
            
        },

        /**出现编辑弹框 */
        showEdit() {
            this.toastData.dialogVisible = true;
            this.toastData.btnData = [
                {
                    name:'保存',//按钮名
                    method:'saveEdit',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }
            ];
            for(var key in this.toastData.form) {
                this.toastData.form[key] = this.tableConfig.currentSelectArr[0][key];
            }
        },

        /**保存编辑 */
        saveEdit() {
            this.$refs.defineDialog.$refs.equipmentSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        id: this.tableConfig.currentSelectArr[0].id,
                        diviceNo: this.toastData.form.diviceNo,
                        communicationType: this.toastData.form.communicationType,
                        ip: this.toastData.form.ip,
                        catNo: this.toastData.form.catNo,
                        macAddr: this.toastData.form.macAddr,
                        brand: this.toastData.form.brand,
                        plc: this.toastData.form.plc,
                        ym: this.toastData.form.ym,
                        sfVersion: this.toastData.form.sfVersion,
                        maxX: this.toastData.form.maxX,
                        maxY: this.toastData.form.maxY,
                        maxZ: this.toastData.form.maxZ,
                        factory: this.toastData.form.factory,
                        storeAreaId: this.toastData.form.storeAreaId,
                    };
                    postWay(obj,this,'/deviceDefine/addOrUpdate').then(data=>{
                        this.toastData.dialogVisible = false;
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
            
        },

        /**删除 */
        remove() {
            this.$confirm("此操作将永久删除该节点设置，是否继续？", "提示", {
                confimButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                var arr = [];
                this.tableConfig.currentSelectArr.forEach(v => {
                    arr.push(v.id);
                });
                var obj = {ids: arr.join(',')};
                postWay(obj,this,'/deviceDefine/delete').then(data=>{
                    console.log(obj)
                    this.$message({
                        type: 'success',
                        message: '删除成功'
                    })
                    this.search();
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                })
            })
        },

        /**添加单路径 */
        addRoute(val) {
            this.singleToastData.dialogVisible = true;
            for(var key in this.singleToastData.form) {
                this.singleToastData.form[key] = val[key];
            }
        },
    },

    watch: {
        "tableConfig.currentSelectArr": function(val) {
            if(val.length == 0) {
                this.tableConfig.toolbarConfig[1].disabled = true;
                this.tableConfig.toolbarConfig[2].disabled = true;
            }else if(val.length == 1) {
                this.tableConfig.toolbarConfig[1].disabled = false;
                this.tableConfig.toolbarConfig[2].disabled = false;
            }else if(val.length > 1) {
                this.tableConfig.toolbarConfig[1].disabled = true;
                this.tableConfig.toolbarConfig[2].disabled = false;
            }
        }
    }
}
</script>
<style lang="less">
    .define {
        position: absolute;
        top: 120px;
        bottom: 20px;
        left: 20px;
        right: 20px;
        background: #fff;
        padding: 20px;
        box-sizing: border-box;
        overflow: auto;

        .myTable {
        }
    }
</style>



