<template>
    <div class="pathSettingDialog">
        <el-form :model="form" label-width="120px" :form="form">
            <el-row>
                <el-col :span="8">
                    <el-form-item label="设备编码：" prop="macNo">
                        <span>{{form.macNo}}</span>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="动作号：" prop="activeNo">
                        <span>{{form.activeNo}}</span>
                    </el-form-item>
                </el-col> 
                <el-col :span="8">
                    <el-form-item label="动作名：" prop="activeName">
                        <span>{{form.activeName}}</span>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="x：" prop="x">
                        <span>{{form.x}}</span>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="y：" prop="y">
                        <span>{{form.y}}</span>
                    </el-form-item>
                </el-col> 
                <el-col :span="8">
                    <el-form-item label="z：" prop="z">
                        <span>{{form.z}}</span>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="顺序号：" prop="serial">
                        <span>{{form.serial}}</span>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="偏移量：" prop="skewing">
                        <span>{{form.skewing}}</span>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <el-button size="mini" @click="add">新增</el-button>
        <el-button size="mini" @click="remove" :disabled="selectRow.length==0">删除</el-button>
        <el-table :data="keyTableData" style="width:100%" @select="select" @select-all="select">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="limitMacNo" label="先决设备">
                <template slot-scope="scope">
                    <el-select size="mini" v-model="scope.row.limitMacNo" @change="equipChange(scope.row.limitMacNo,scope.$index)">
                        <el-option v-for="item in scope.row.macNoList" :label="item.diviceNo" :value="item.diviceNo"></el-option>
                    </el-select>
                </template>
            </el-table-column>
            <el-table-column prop="limitMem" label="先决通讯键">
                <template slot-scope="scope">
                    <el-select size="mini" v-model="scope.row.limitMem" @change="keyChange(scope.row.limitMem,scope.$index)">
                        <el-option v-for="item in scope.row.keyList" :label="item.keyType" :value="item.keyType"></el-option>
                    </el-select>
                </template>
            </el-table-column>
            <el-table-column prop="limitValue" label="先决通讯键值">
                <template slot-scope="scope">
                    <el-select size="mini" v-model="scope.row.limitValue">
                        <el-option v-for="item in scope.row.keyValueList" :label="item.keyValue" :value="item.keyValue"></el-option>
                    </el-select>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
export default {
    props: ["form", "macNoList", "keyList", "keyValueList", "tableData"],

    data() {
        return {
            selectRow: [],
            NoId: 0,
        }
    },

    computed: {
        keyTableData: function() {
            var copyTable = JSON.parse(JSON.stringify(this.tableData));
            copyTable.forEach((v,i) => {
                v.macNoList = this.macNoList;
                v.keyList = [];
                v.keyValueList = [];
                v.NoId = i;
            });
            return copyTable;
        },
    },

    mounted() {
        if(this.keyTableData.length > 0) {
            this.NoId = this.keyTableData.length;
        }

        /**页面初始化 */
        this.keyTableData.forEach((v,i) => {
            if(v.limitMacNo) {
                this.$emit('equipChange', v.limitMacNo, i);
            }
            if(v.limitMem) {
                this.$emit('keyChange', v.limitMem, i)
            }
        })
    },

    methods: {
        add() {
            this.keyTableData.push({NoId: this.NoId++, macNoList: this.macNoList, keyList: [], keyValueList: [], limitMacNo: '', limitMem: '', limitValue: ''});
        },

        select(val,row) {
            this.selectRow = val;
        },

        remove() {
            this.selectRow.forEach(val => {
                this.keyTableData.forEach((v,i) => {
                    if(v.NoId == val.NoId) {
                        this.keyTableData.splice(i,1);
                    }
                })
            })
        },

        equipChange(val,index) {
            console.log(val,index)
            this.$emit('equipChange',val,index);
            this.keyTableData[index].limitMem = "";
            this.keyTableData[index].limitValue = "";
            this.keyTableData[index].keyList = [];
            this.keyTableData[index].keyValueList = [];
        },

        keyChange(val,index) {
            console.log(val,index)
            this.$emit('keyChange',val,index);
            this.keyTableData[index].limitValue = "";
            this.keyTableData[index].keyValueList = [];
        }
    }
}
</script>
<style lang="less">
    .pathSettingDialog {

    }
</style>



