import { setStore, getStore, removeStore } from '../assets/store'
function setFistTag(list) {
    if (list.length == 1) {
        list[0].close = false;
    } else {
        list.some(a => {
            a.close = true
        })
    }
    return list;

}
const tagObj = {
    label: '',
    value: '',
    query: '',
    num: '',
    close: true,
}
export default {
    ADD_TAG: (state, action) => {
        state.tag = action;
        setStore({ name: 'tag', content: state.tag, type: 'session' })
        if (state.tagList.some(a => a.value === action.value)) return
        state.tagList.push({
            label: action.label,
            value: action.value,
            query: action.query,
        })
        state.tagList = setFistTag(state.tagList);
        setStore({ name: 'tagList', content: state.tagList, type: 'session' })
    },
    SET_TAG_CURRENT: (state, tagCurrent) => {
        console.log('tagCurrent',tagCurrent)
        state.tagCurrent = tagCurrent;
        setStore({ name: 'tagCurrent', content: state.tagCurrent, type: 'session' })
    },
    SET_TAG: (state, value) => {
        state.tagList.forEach((ele, num) => {
            if (ele.value === value) {
                state.tag = state.tagList[num];
                setStore({ name: 'tag', content: state.tag, type: 'session' })
            }
        });
    },
    DEL_ALL_TAG: (state, action) => {
        state.tag = tagObj;
        state.tagList = [];
        state.tagList.push(state.tagWel);
        state.tagList[0].close = false;
        removeStore({ name: 'tag' });
        removeStore({ name: 'tagList' });
    },
    DEL_TAG_OTHER: (state, action) => {
        state.tagList.forEach((ele, num) => {
            if (ele.value === state.tag.value) {
                state.tagList = state.tagList.slice(num, num + 1)
                state.tag = state.tagList[0];
                state.tagList[0].close = false;
                setStore({ name: 'tag', content: state.tag, type: 'session' })
                setStore({ name: 'tagList', content: state.tagList, type: 'session' })
            }
        })

    },
    DEL_TAG: (state, action) => {
        state.tagList.forEach((ele, num) => {
            if (ele.value === action.value) {
                state.tagList.splice(num, 1)
                state.tagList = setFistTag(state.tagList);
                setStore({ name: 'tag', content: state.tag, type: 'session' })
                setStore({ name: 'tagList', content: state.tagList, type: 'session' })
            }
        })
    },
    SET_COLLAPSE: (state, action) => {
        state.isCollapse = !state.isCollapse;
    },
    SET_LOCK_PASSWD: (state, lockPasswd) => {
        state.lockPasswd = lockPasswd;
        console.log(state)
        setStore({
            name: 'lockPasswd',
            content: state.lockPasswd,
            type: 'session'
        })
    },
    CLEAR_LOCK: (state, action) => {
        state.isLock = false;
        state.lockPasswd = '';
        removeStore({
            name: 'lockPasswd'
        });
        removeStore({
            name: 'isLock'
        });
    },
    SET_LOCK: (state, action) => {
        state.isLock = true;
        setStore({
            name: 'isLock',
            content: state.isLock,
            type: 'session'
        })
    },
    SET_THEME: (state, color) => {
        state.theme = color;
        setStore({
            name: 'theme',
            content: state.theme,
        })
    },
    SET_THEME_NAME: (state, themeName) => {
        state.themeName = themeName;
        setStore({
            name: 'themeName',
            content: state.themeName,
        })
    },
}




