<template>
    <div class="logistics-control">
        <search :data="searchData" :config="searchConfig" @search="search"></search>
        <set-table :tableConfig="tableConfig" :tableData="tableData" :toolbarConfig="tableConfig.toolbarConfig" @showAdd="showAdd" @showEdit="showEdit" @remove="remove" @pageChange="pageChange" @addRoute="addRoute"></set-table>
        <!-- 设备动作设置弹框 -->
        <toast :config="toastData" @cancel="toastData.dialogVisible=false" @saveAdd="saveAdd" @savaAgain="savaAgain" @saveEdit="saveEdit">
            <action-settingDialog :form="toastData.form" :macNoList="searchConfig[0][0].childrens" :siteTypeList="toastData.siteTypeList" ref="actionsettingDialog"></action-settingDialog>
        </toast>
        <!-- 设置动作条件限制弹框 -->
        <toast :config="singleToastData" @cancelSingle="singleToastData.dialogVisible=false" @saveAddSingle="saveAddSingle">
            <limit-settingDialog v-if="singleToastData.flag" :form="singleToastData.form" :tableData="singleToastData.tableData" :macNoList="singleToastData.macNoList" :keyList="singleToastData.keyList" :keyValueList="singleToastData.keyValueList" @equipChange="equipChange" @keyChange="keyChange" ref="limitsettingDialog"></limit-settingDialog>
        </toast>
    </div>
</template>
<script>
import search from '../../components/search'
import toolbar from '../../components/toolbar'
import setTable from '../../components/table'
import toast from '../../components/toast'
import actionSettingDialog from './actionSettingDialog'
import limitSettingDialog from './limitSettingDialog'
import {postWay,getWay,postJsonWay} from './interface.js'
export default {
    data() {
        return {
            /**搜索框配置 */
            searchData: {},
            searchConfig: [
                [
                    {label: "设备编码", field: "macNo", type: "select", childrens: []}
                ]
            ],

            /**表格配置 */
            tableConfig: {
                toolbarConfig: [
                    {icon: "", disabled: false, method: "showAdd", name: "新增"},
                    {icon: "", disabled: true, method: "showEdit", name: "编辑"},
                    {icon: "", disabled: true, method: "remove", name: "删除"}
                ],
                height: "430",
                isHigh: false,
                isSelection: true,
                align: "center",
                isOrder: false,
                currentSelectArr: [],
                currentClickRow: {},
                colConfig: [
                    {
                        fixed: false,
                        field: "macNo",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "设备编码",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "activeNo",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "动作号",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "activeName",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "动作名",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "serial",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "顺序号",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "x",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "x",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "y",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "y",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "z",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "z",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "skewing",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "偏移量",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "",
                        width: "200",
                        isSort: false,
                        isResize: "",
                        noTooltip: "操作",
                        type: "btnsText",
                        btnData: [
                            {name: '设置动作条件限制', method: 'addRoute'}
                        ]
                    }
                ],
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0
            },

            tableData: [{}],

            /**弹出框配置 */
            toastData: {
                title:'设备动作设置',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'980px',//弹窗宽
                btnData:[{
                    name:'保存',//按钮名
                    method:'saveAdd',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'保存并继续',
                    method:'savaAgain',
                    bgColor:'dialog-blue',
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }],

                form: {
                    macNo: '',
                    activeNo: '',
                    activeName: '',
                    x: '',
                    y: '',
                    z: '',
                    serial: '',
                    skewing: '',
                },

                mac_NoList: []
            },

            singleToastData: {
                title:'设置动作条件限制',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'980px',//弹窗宽
                btnData:[{
                    name:'保存',//按钮名
                    method:'saveAddSingle',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'取消',
                    method:'cancelSingle',
                    bgColor:'dialog-normal',
                    isShow: true
                }],

                form: {
                    macNo: '',
                    activeNo: '',
                    activeName: '',
                    x: '',
                    y: '',
                    z: '',
                    serial: '',
                    skewing: ''
                },

                flag: false,

                tableData: [],
                macNoList: [],
                keyList: [],
                keyValueList: []
            },
        }
    },

    components: {
      search,
      toolbar,
      setTable,
      toast,
      actionSettingDialog,
      limitSettingDialog,
    },

    mounted() {
        this.selectList();

        /**设备编码下拉查询 */
        getWay({},this,'/sitSet/optionDeviceNo').then(data=>{
            data.forEach(v=> {
                v.name = v.diviceNo;
                v.value = v.diviceNo;
            }) 
            this.searchConfig[0][0].childrens = data;
            this.singleToastData.macNoList = data;
        })

        /**先决设备下拉查询 */
        postWay({},this,'/commuKeySet/optionKey').then(data=>{
            console.log(data)
        })
    },

    methods: {
        /**分页查询 */
        selectList() {
            var obj = {
                pageNo: this.tableConfig.pageNo,
                pageSize: this.tableConfig.pageSize,
                macNo: this.searchData.macNo,
            };
            postWay(obj,this,'/motionSet/page').then(data=>{
                this.tableData = data.items;
                this.tableConfig.total = data.numRows;
            })
        },

        /**按搜索条件查询 */
        search() {
            this.tableConfig.pageNo = 1;
            this.tableConfig.pageSize = 20;
            this.selectList();
        },
        
        /**分页条页码、条数改变 */
        pageChange() {
            this.selectList();
        },

        /**出现新增弹框 */
        showAdd() {
            this.toastData.dialogVisible = true;
            if(this.$refs.actionsettingDialog && this.$refs.actionsettingDialog.$refs.actionSettingForm) {
                this.$refs.actionsettingDialog.$refs.actionSettingForm.resetFields();
            }
            this.toastData.btnData = [
                {
                    name:'保存',//按钮名
                    method:'saveAdd',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'保存并继续',
                    method:'savaAgain',
                    bgColor:'dialog-blue',
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }
            ];
            for(var key in this.toastData.form) {
                this.toastData.form[key] = "";
            }
        },

        /**保存新增 */
        saveAdd() {
            this.$refs.actionsettingDialog.$refs.actionSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        macNo: this.toastData.form.macNo,
                        activeNo: this.toastData.form.activeNo,
                        activeName: this.toastData.form.activeName,
                        x: this.toastData.form.x,
                        y: this.toastData.form.y,
                        z: this.toastData.form.z,
                        serial: this.toastData.form.serial,
                        skewing: this.toastData.form.skewing,
                    };
                    postWay(obj,this,'/motionSet/addOrUpdate').then(data=>{
                        this.toastData.dialogVisible = false;
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
            
        },

        /**保存新增并继续 */
        savaAgain() {
            this.$refs.actionsettingDialog.$refs.actionSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        macNo: this.toastData.form.macNo,
                        activeNo: this.toastData.form.activeNo,
                        activeName: this.toastData.form.activeName,
                        x: this.toastData.form.x,
                        y: this.toastData.form.y,
                        z: this.toastData.form.z,
                        serial: this.toastData.form.serial,
                        skewing: this.toastData.form.skewing,
                    };
                    postWay(obj,this,'/motionSet/addOrUpdate').then(data=>{
                        this.toastData.dialogVisible = false;
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        })
                        for(var key in this.toastData.form) {
                            this.toastData.form[key] = "";
                        }
                        this.$nextTick(() => {
                            this.$refs.actionsettingDialog.$refs.actionSettingForm.resetFields();
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
        },

        /**出现编辑弹框 */
        showEdit() {
            this.toastData.dialogVisible = true;
            if(this.$refs.actionsettingDialog && this.$refs.actionsettingDialog.$refs.actionSettingForm) {
                this.$refs.actionsettingDialog.$refs.actionSettingForm.resetFields();
            }
            this.toastData.btnData = [
                {
                    name:'保存',//按钮名
                    method:'saveEdit',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }
            ];
            for(var key in this.toastData.form) {
                this.toastData.form[key] = this.tableConfig.currentSelectArr[0][key];
            }
        },

        /**保存编辑 */
        saveEdit() {
            this.$refs.actionsettingDialog.$refs.actionSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        id: this.tableConfig.currentSelectArr[0].id,
                        macNo: this.toastData.form.macNo,
                        activeNo: this.toastData.form.activeNo,
                        activeName: this.toastData.form.activeName,
                        x: this.toastData.form.x,
                        y: this.toastData.form.y,
                        z: this.toastData.form.z,
                        serial: this.toastData.form.serial,
                        skewing: this.toastData.form.skewing,
                    };
                    postWay(obj,this,'/motionSet/addOrUpdate').then(data=>{
                        this.toastData.dialogVisible = false;
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
        },

        /**删除 */
        remove() {
            this.$confirm("此操作将永久删除该节点设置，是否继续？", "提示", {
                confimButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                var arr = [];
                this.tableConfig.currentSelectArr.forEach(v => {
                    arr.push(v.id);
                });
                var obj = {ids: arr.join(',')};
                postWay(obj,this,'/motionSet/delete').then(data=>{
                    console.log(obj)
                    this.$message({
                        type: 'success',
                        message: '删除成功'
                    })
                    this.search();
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                })
            })
        },

        /**添加单路径 */
        addRoute(val) {
            this.singleToastData.dialogVisible = true;
            this.tableConfig.currentClickRow = val;
            var obj = {id: val.id};
            postWay(obj,this,'/motionSet/findDetail').then(data=>{
                this.singleToastData.flag = true;
                this.singleToastData.tableData = data.list;
            })
            for(var key in this.singleToastData.form) {
                this.singleToastData.form[key] = val[key];
            }
        },

        /**单路径保存 */
        saveAddSingle() {
            var flag = 'T';
            var arr = [];
            this.$refs.limitsettingDialog.keyTableData.forEach(v => {
                arr.push({deviceId: this.tableConfig.currentClickRow.id, limitMacNo: v.limitMacNo, limitMem: v.limitMem, limitValue: v.limitValue})
                if(!v.limitMacNo || !v.limitMem || !v.limitValue) {
                    flag = 'F'
                }
            })
            if(flag == 'T') {
                postJsonWay(arr,this,'/activeLimitWcs/addOrUpdate').then(data=>{
                    this.singleToastData.dialogVisible = false;
                    this.$message({
                        type: 'success',
                        message: '保存成功'
                    })
                    this.search();
                })
            }else {
                this.$message({
                    type: 'warning',
                    message: '请将限制设置完整！'
                })
            }
            
        },

        equipChange(val,index) {
            var obj = {macNo: val}
            postWay(obj,this,'/commuKeySet/optionKey').then(data=>{
                this.$refs.limitsettingDialog.keyTableData[index].keyList = data;
            })
        },

        keyChange(val,index) {
            var obj = {keyType: val}
            postWay(obj,this,'/commuKeyvalueSet/optionValueByKeyId').then(data=>{
                this.$refs.limitsettingDialog.keyTableData[index].keyValueList = data;
            })
        }
    },

    watch: {
        "tableConfig.currentSelectArr": function(val) {
            if(val.length == 0) {
                this.tableConfig.toolbarConfig[1].disabled = true;
                this.tableConfig.toolbarConfig[2].disabled = true;
            }else if(val.length == 1) {
                this.tableConfig.toolbarConfig[1].disabled = false;
                this.tableConfig.toolbarConfig[2].disabled = false;
            }else if(val.length > 1) {
                this.tableConfig.toolbarConfig[1].disabled = true;
                this.tableConfig.toolbarConfig[2].disabled = false;
            }
        },

        "singleToastData.dialogVisible": function(val) {
            if(!val) {
                this.singleToastData.flag = false;
            }
        }
    }
}
</script>
<style lang="less">
    .logistics-control{
        height: 100%;
        overflow: auto;
    }
</style>



