<template>
    <div class="nodeSettingDialog">
        <el-form :model="form" :rules="rules" label-width="120px" ref="nodeSettingForm" :form="form" :macNoList="macNoList" :siteTypeList="siteTypeList" size="mini">
            <el-row>
                <el-col :span="8">
                    <el-form-item label="设备编码" prop="macNo">
                        <el-select size="mini" v-model="form.macNo">
                            <el-option v-for="item in macNoList" :label="item.name" :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="节点名" prop="siteName">
                        <el-input size="mini" v-model.trim="form.siteName"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="生产设备编码" prop="prodMacNo">
                        <el-select size="mini" v-model="form.prodMacNo" @change="prodMacNoChange">
                            <el-option v-for="item in prodMacNoList" :label="item.deviceNo+' | '+item.deviceName" :value="item.deviceNo"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="节点类型" prop="siteType">
                        <el-select size="mini" v-model="form.siteType">
                            <el-option v-for="item in siteTypeList" :label="item.name" :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="内置节点编码" prop="macInSiteNo">
                        <el-input size="mini" v-model.trim="form.macInSiteNo"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="层数" prop="layer">
                        <el-input-number size="mini" v-model="form.layer" :controls="false"></el-input-number>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>
<script>
import {optionProductionDeviceNo} from './interface.js'
export default {
    props: ["form", "macNoList", "siteTypeList"],

    data() {
        /**小数校验（保留两位小数） */
        var vlidateDecimal = (rule, value, callback) => {
            var reg = /(^\d+)|(^(-\d+))\.(\d{2})$/;
            if(value) {
                if(reg.test(value)) {
                    callback();
                }else {
                    callback(new Error('请输入小数(保留两位小数)'));
                }
            }else {
                callback();
            }
            
        }
        return {
            prodMacNoList:[],
            rules: {
                macNo: [
                    { required: true, message: '请选择设备编码', trigger: 'change' }
                ],
                siteName: [
                    { required: true, message: '请输入节点名', trigger: 'blur' },
                    { max: 24, message: '最大长度为24个字符', trigger: 'blur' }
                ],
                prodMacNo: [
                     { required: true, message: '请选择生产设备编码', trigger: 'blur' }
                ],
                macInSiteNo: [
                    { required: true, message: '请输入内置节点编码', trigger: 'blur' }
                ],
                siteType: [
                    { required: true, message: '请选择节点类型', trigger: 'change' }
                ],
                layer: [
                    { required: true,type:'number', message: '请输入层数', trigger: 'change' }
                ],
                afterTime: [
                    { required: true, message: '请选择节点类型', trigger: 'change' }
                ],
            }
        }
    },
    methods: {
        /**物料编码值改变 */
        codeChange(val) {
            this.$emit("codeChange", val);
        },
        prodMacNoChange(data){
            if(data){
                this.form.deviceName=this.prodMacNoList[this.prodMacNoList.findIndex(val=>val.deviceNo==data)].deviceName
            }else{
                this.form.deviceName=''
            }
        },
        /**生产设备编码值改变 */
        equipmentChange(val) {
            this.$emit("equipmentChange", val);
        }
    },
    mounted(){
        optionProductionDeviceNo().then(res=>{
            this.prodMacNoList=res
        })
    }
}
</script>
<style lang="less">
    .nodeSettingDialog {
        .el-input-number.is-without-controls .el-input__inner {
            width: 150%;
        }
    }
</style>



