import http from '../../api/base'
export let upDownGet = (param)=>{
    return http.post('systemTask/upDownGet',param)
}
export let upDown = (param)=>{
    return http.post('taskSingle/upDown',param)
}
export let optionSite = (param)=>{
    return http.get('sitSet/optionSite',param)
}
export let upDownAdd = (param)=>{
    return http.post('taskSingle/upDownAdd',param)
}
export let update = (param)=>{
    return http.post('taskSingle/update',param)
}
export let del = (param)=>{
    return http.post('taskSingle/del',param)
}
export let moveBoxGet = (param)=>{
    return http.post('systemTask/moveBoxGet',param)
}
export let moveBox = (param)=>{
    return http.post('taskSingle/moveBox',param)
}
export let moveBoxAdd = (param)=>{
    return http.post('taskSingle/moveBoxAdd',param)
}
export let startMission = (param)=>{
    return http.post('dispatcher/startMission',param)
}


