<template>
    <div class="pathSettingDialog">
        <el-form :model="form" :rules="rules" label-width="120px" ref="pathSettingForm" :form="form">
            <el-row>
                <el-col :span="8">
                    <el-form-item label="设备编码" prop="macNo">
                        <el-select size="mini" v-model="form.macNo">
                            <el-option v-for="item in macNoList" :label="item.name" :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="路径号" prop="activeNo">
                        <el-input size="mini" v-model.trim="form.activeNo"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="路径名" prop="activeName">
                        <el-input size="mini" v-model.trim="form.activeName"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="开始节点" prop="startSiteId">
                        <el-select size="mini" v-model="form.startSiteId">
                            <el-option v-for="item in optionList" :label="item.siteName" :value="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="下一节点" prop="endSiteId">
                        <el-select size="mini" v-model="form.endSiteId">
                            <el-option v-for="item in optionList" :label="item.siteName" :value="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="偏移量" prop="offset">
                        <el-input size="mini" v-model.trim="form.offset"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="需要时间" prop="needTime">
                        <el-input size="mini" v-model.trim="form.needTime"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            
        </el-form>
    </div>
</template>
<script>
export default {
    props: ["form","macNoList", "optionList"],

    data() {
        var validateNumber = (rule,value,callback) => {
            var reg = /^\d+$/;
            if(value) {
                if(reg.test(value)) {
                    callback();
                }else {
                    callback(new Error('请输入数字'))
                }
            }else {
                callback();
            }
            
        }

        var validateActiveName = (rule, value, callback) => {
            var reg = /^[a-zA-Z\u4E00-\uFA29]+$/;
            if(reg.test(value)) {
                callback();
            }else {
                callback(new Error('请输入中文或英文字符'));
            }
        }

        /**小数校验（保留两位小数） */
        var vlidateDecimal = (rule, value, callback) => {
            var reg = /(^\d+)|(^(-\d+))\.(\d{2})$/;
            if(value) {
                if(reg.test(value)) {
                    callback();
                }else {
                    callback(new Error('请输入小数(保留两位小数)'));
                }
            }else {
                callback();
            }
        }
        return {
            rules: {
                macNo: [
                    { required: true, message: '请选择设备编码', trigger: 'change' }
                ],
                activeNo: [
                    { required: true, message: '请输入路径号', trigger: 'blur' },
                    // { max: 11, message: '最大长度为11个字符', trigger: 'blur' },
                    { validator: validateNumber, trigger: 'blur' }
                ],
                activeName: [
                    { required: true, message: '请输入路径名', trigger: 'blur' },
                    { max: 24, message: '最大长度为24个字符', trigger: 'blur' },
                    { validator: validateActiveName, trigger: 'blur' }
                ],
                startSiteId: [
                    { required: true, message: '请选择开始节点', trigger: 'change' }
                ],
                endSiteId: [
                    { required: true, message: '请选择下一节点', trigger: 'change' }
                ],
                offset: [
                    { validator: vlidateDecimal, trigger: 'blur' }
                ],
                needTime: [
                    // { max: 11, message: '最大长度为11个字符', trigger: 'blur' },
                    { validator: validateNumber, trigger: 'blur' }
                ]
            }
        }
    }
}
</script>
<style lang="less">
    .pathSettingDialog {

    }
</style>



