<template>
    <div class="logistics-control">
        <search :data="searchData" :config="searchConfig" @search="search"></search>
        <set-table :tableConfig="tableConfig" :tableData="tableData" :toolbarConfig="tableConfig.toolbarConfig" @showAdd="showAdd" @showEdit="showEdit" @remove="remove" @pageChange="pageChange"></set-table>
        <toast :config="toastData" @cancel="toastData.dialogVisible=false" @saveAdd="saveAdd" @savaAgain="savaAgain" @saveEdit="saveEdit">
            <node-settingDialog :form="toastData.form" :macNoList="searchConfig[0][0].childrens" :siteTypeList="searchConfig[0][1].childrens" ref="nodsettingDialog" @codeChange="codeChange" @equipmentChange="equipmentChange"></node-settingDialog>
        </toast>
    </div>
</template>
<script>
import search from '../../components/search'
import toolbar from '../../components/toolbar'
import setTable from '../../components/table'
import toast from '../../components/toast'
import nodeSettingDialog from './nodeSettingDialog'
import {postWay,getWay} from './interface.js'
export default {
    data() {
        return {
            /**搜索框配置 */
            searchData: {},
            searchConfig: [
                [
                    {label: "设备编码", field: "macNo", type: "select", childrens: []},
                    {label: "节点类型", field: "siteType", type: "select", childrens: [{name: '节点', value: 0},{name: '上料位', value: 1},{name: '下料位', value: 2}]}
                ]
            ],

            /**表格配置 */
            tableConfig: {
                toolbarConfig: [
                    {icon: "", disabled: false, method: "showAdd", name: "新增"},
                    {icon: "", disabled: true, method: "showEdit", name: "编辑"},
                    {icon: "", disabled: true, method: "remove", name: "删除"}
                ],
                height: "430",
                isHigh: false,
                isSelection: true,
                align: "center",
                isOrder: false,
                currentSelectArr: [],
                colConfig: [
                    {
                        fixed: false,
                        field: "macNo",
                        isSort: false,
                        isResize: "",
                        noTooltip: "设备编码",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "siteName",
                        isSort: false,
                        isResize: "",
                        noTooltip: "节点名",
                        type: "text",
                    },
                    // {
                    //     fixed: false,
                    //     field: "offset",
                    //     width: "",
                    //     isSort: false,
                    //     isResize: "",
                    //     noTooltip: "偏移量",
                    //     type: "text",
                    // },
                    // {
                    //     fixed: false,
                    //     field: "",
                    //     width: "",
                    //     isSort: false,
                    //     isResize: "",
                    //     noTooltip: "是否为空",
                    //     type: "text",
                    // },
                    {
                        fixed: false,
                        field: "matterName",
                        isSort: false,
                        isResize: "",
                        noTooltip: "物料信息",
                        type: "text",
                    },
                    // {
                    //     fixed: false,
                    //     field: "afterTime",
                    //     isSort: false,
                    //     isResize: "",
                    //     noTooltip: "后置时间",
                    //     type: "text",
                    // },
                    {
                        fixed: false,
                        field: "prodMacMsg",
                        isSort: false,
                        isResize: "",
                        noTooltip: "生产设备信息",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "siteTypeName",
                        isSort: false,
                        isResize: "",
                        noTooltip: "节点类型",
                        type: "text",
                    }
                ],
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0
            },

            tableData: [],

            /**弹出框配置 */
            toastData: {
                title:'设备节点设置',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'980px',//弹窗宽
                btnData:[{
                    name:'保存',//按钮名
                    method:'dialogSuer',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'保存并继续',
                    method:'savaAgain',
                    bgColor:'dialog-blue',
                    isShow: true
                },{
                    name:'取消',
                    method:'savaAgain',
                    bgColor:'dialog-normal',
                    isShow: true
                }],

                form: {
                    macNo: '',
                    siteName: '',
                    prodMacNo:'',
                    siteType: '',
                    macInSiteNo:'',
                    layer:0,
                    deviceName:'',
                    afterTime:'',
                    id:0
                }
            },
        }
    },

    components: {
      search,
      toolbar,
      setTable,
      toast,
      nodeSettingDialog
    },

    mounted() {
        this.selectList();

        /**设备编码下拉查询 */
        getWay({},this,'/sitSet/optionDeviceNo').then(data=>{
            data.forEach(v=> {
                v.name = v.diviceNo;
                v.value = v.diviceNo;
            }) 
            this.searchConfig[0][0].childrens = data;
        })
    },

    methods: {
        /**分页查询 */
        selectList() {
            var obj = {
                pageNo: this.tableConfig.pageNo,
                pageSize: this.tableConfig.pageSize,
                macNo: this.searchData.macNo,
                siteType: this.searchData.siteType,
            };
            postWay(obj,this,'/sitSet/page').then(data=>{
                console.log(data.items,'---')
                this.tableData = data.items;
                this.tableConfig.total = data.numRows;
            })
        },

        /**按搜索条件查询 */
        search() {
            this.tableConfig.pageNo = 1;
            this.tableConfig.pageSize = 20;
            this.selectList();
        },
        
        /**分页条页码、条数改变 */
        pageChange() {
            this.selectList();
        },

        /**出现新增弹框 */
        showAdd() {
            this.toastData.dialogVisible = true;
            if(this.$refs.nodsettingDialog && this.$refs.nodsettingDialog.$refs.nodeSettingForm) {
                this.$refs.nodsettingDialog.$refs.nodeSettingForm.resetFields();
            }
            this.toastData.btnData = [
                {
                    name:'保存',//按钮名
                    method:'saveAdd',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'保存并继续',
                    method:'savaAgain',
                    bgColor:'dialog-blue',
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }
            ];
            for(var key in this.toastData.form) {
                this.toastData.form[key] = null;
            }
        },

        /**保存新增 */
        saveAdd() {
            this.$refs.nodsettingDialog.$refs.nodeSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        ...this.toastData.form
                    };
                    postWay(obj,this,'/sitSet/addOrUpdate').then(data=>{
                        this.toastData.dialogVisible = false;
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
            
        },

        /**保存新增并继续 */
        savaAgain() {
            this.$refs.nodsettingDialog.$refs.nodeSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        ...this.toastData.form
                    };
                    postWay(obj,this,'/sitSet/addOrUpdate').then(data=>{
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        });
                        for(var key in this.toastData.form) {
                            this.toastData.form[key] = "";
                        }
                        this.$nextTick(() => {
                            this.$refs.nodsettingDialog.$refs.nodeSettingForm.resetFields();
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
            
        },

        /**出现编辑弹框 */
        showEdit() {
            this.toastData.dialogVisible = true;
            if(this.$refs.nodsettingDialog && this.$refs.nodsettingDialog.$refs.nodeSettingForm) {
                this.$refs.nodsettingDialog.$refs.nodeSettingForm.resetFields();
            }
            this.toastData.btnData = [
                {
                    name:'保存',//按钮名
                    method:'saveEdit',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }
            ];
            for(var key in this.toastData.form) {
                this.toastData.form[key] = this.tableConfig.currentSelectArr[0][key];
            }
        },

        /**保存编辑 */
        saveEdit() {
            this.$refs.nodsettingDialog.$refs.nodeSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        id: this.tableConfig.currentSelectArr[0].id,
                        macNo: this.toastData.form.macNo,
                        siteType: this.toastData.form.siteType,
                        siteName: this.toastData.form.siteName,
                        offset: this.toastData.form.offset,
                        x: this.toastData.form.x,
                        y: this.toastData.form.y,
                        z: this.toastData.form.z,
                    };
                    postWay(obj,this,'/sitSet/addOrUpdate').then(data=>{
                        this.toastData.dialogVisible = false;
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
            
        },

        /**删除 */
        remove() {
            this.$confirm("此操作将永久删除该节点设置，是否继续？", "提示", {
                confimButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                var arr = [];
                this.tableConfig.currentSelectArr.forEach(v => {
                    arr.push(v.id);
                });
                var obj = {ids: arr.join(',')};
                postWay(obj,this,'/sitSet/delete').then(data=>{
                    console.log(data)
                    this.$message({
                        type: 'success',
                        message: '删除成功'
                    })
                    this.search();
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                })
            })
        },

        codeChange(val) {
            console.log(val,'9999')
        },

        equipmentChange(val) {
            console.log(val)
        }
    },

    watch: {
        "tableConfig.currentSelectArr": function(val) {
            if(val.length == 0) {
                this.tableConfig.toolbarConfig[1].disabled = true;
                this.tableConfig.toolbarConfig[2].disabled = true;
            }else if(val.length == 1) {
                this.tableConfig.toolbarConfig[1].disabled = false;
                this.tableConfig.toolbarConfig[2].disabled = false;
            }else if(val.length > 1) {
                this.tableConfig.toolbarConfig[1].disabled = true;
                this.tableConfig.toolbarConfig[2].disabled = false;
            }
        }
    }
}
</script>



