<template>
    <div class="communication-setdialog">
        <el-form :model="form" label-width="120px" :rules="rules" ref="comForm">
            <el-row>
                <el-col :span="8">
                    <el-form-item label="设备编码" prop="macNo">
                        <el-select size="mini" v-model="form.macNo">
                            <el-option v-for="item in macNoList" :value="item.value" :label="item.name"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="通讯键" prop="keyType">
                        <el-input size="mini" v-model="form.keyType"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="产量标识" prop="isYield">
                        <el-select size="mini" v-model="form.isYield">
                            <el-option :value=1 label="其他参数"></el-option>
                            <el-option :value=0 label="产量"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="上/下行标志" prop="direction">
                        <el-select size="mini" v-model="form.direction">
                            <el-option :value=1 label="下行"></el-option>
                            <el-option :value=0 label="上行"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>
<script>
export default {
    props: ["form", "macNoList"],
    data() {
        return {
            rules: {
                macNo: [
                    { required: true, message: '请选择设备编码', trigger: 'change'}
                ],
                keyType: [
                    { required: true, message: '请输入通讯键', trigger: 'blur'}
                ],
                isYield: [
                    { required: true, message: '请选择产量标识', trigger: 'change'}
                ],
                direction: [
                    { required: true, message: '请选择上/下行标志', trigger: 'change'}
                ],
            }
        }
    }
}
</script>
<style lang="less">
    .communication-setdialog {

    }
</style>



