<template>
    <div class="logistics-control">
        <search :data="searchData" :config="searchConfig" @search="search"></search>
        <set-table :tableConfig="tableConfig" :tableData="tableData" :toolbarConfig="tableConfig.toolbarConfig" @showAdd="showAdd" @showEdit="showEdit" @remove="remove" @pageChange="pageChange"></set-table>
        <toast :config="toastData" @cancel="toastData.dialogVisible=false" @saveAdd="saveAdd" @savaAgain="savaAgain" @saveEdit="saveEdit">
            <path-settingDialog :macNoList="searchConfig[0][0].childrens" :form="toastData.form" :optionList="toastData.optionList" ref="pathSettingDialog"></path-settingDialog>
        </toast>
    </div>
</template>
<script>
import search from '../../components/search'
import toolbar from '../../components/toolbar'
import setTable from '../../components/table'
import toast from '../../components/toast'
import pathSettingDialog from './pathSettingDialog'
import {postWay,getWay} from './interface.js'
export default {
    data() {
        return {
            /**搜索框配置 */
            searchData: {},
            searchConfig: [
                [
                    {label: "设备编码", field: "macNo", type: "select", childrens: []}
                ]
            ],

            /**表格配置 */
            tableConfig: {
                toolbarConfig: [
                    {icon: "", disabled: false, method: "showAdd", name: "新增"},
                    {icon: "", disabled: true, method: "showEdit", name: "编辑"},
                    {icon: "", disabled: true, method: "remove", name: "删除"}
                ],
                height: "430",
                isHigh: false,
                isSelection: true,
                align: "center",
                isOrder: false,
                currentSelectArr: [],
                colConfig: [
                    {
                        fixed: false,
                        field: "macNo",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "设备编码",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "activeNo",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "路径号",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "activeName",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "路径名",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "startPoint",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "开始节点",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "endPoint",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "下一节点",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "offset",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "偏移量",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "needTime",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "需要时间",
                        type: "text",
                    }
                ],
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0
            },

            tableData: [],

            /**弹出框配置 */
            toastData: {
                title:'设备单路径设置',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'980px',//弹窗宽
                btnData:[{
                    name:'保存',//按钮名
                    method:'saveAdd',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'保存并继续',
                    method:'savaAgain',
                    bgColor:'dialog-blue',
                    isShow: true
                },{
                    name:'取消',
                    method:'savaAgain',
                    bgColor:'dialog-normal',
                    isShow: true
                }],

                form: {
                    macNo: '',
                    activeNo: '',
                    activeName: '',
                    startSiteId: '',
                    endSiteId: '',
                    offset: '',
                    needTime: ''
                },

                optionList: []
            },
        }
    },

    components: {
      search,
      toolbar,
      setTable,
      toast,
      pathSettingDialog
    },

    mounted() {
        this.selectList();

        /**设备编码下拉查询 */
        getWay({},this,'/sitSet/optionDeviceNo').then(data=>{
            data.forEach(v=> {
                v.name = v.diviceNo;
                v.value = v.diviceNo;
            }) 
            this.searchConfig[0][0].childrens = data;
        })

        /**节点下拉查询 */
        getWay({},this,'/sitSet/optionSite').then(data=>{
            data.forEach(v=> {
                v.name = v.siteName;
                v.value = v.id;
            }) 
            this.toastData.optionList = data;
        })
    },

    methods: {
        /**分页查询 */
        selectList() {
            var obj = {
                pageNo: this.tableConfig.pageNo,
                pageSize: this.tableConfig.pageSize,
                macNo: this.searchData.macNo,
            };
            postWay(obj,this,'/singlePathSet/page').then(data=>{
                console.log(obj,data,'000')
                this.tableData = data.items;
                this.tableConfig.total = data.numRows;
            })
        },

        /**按搜索条件查询 */
        search() {
            this.tableConfig.pageNo = 1;
            this.tableConfig.pageSize = 20;
            this.selectList();
        },
        
        /**分页条页码、条数改变 */
        pageChange() {
            this.selectList();
        },

        /**出现新增弹框 */
        showAdd() {
            this.toastData.dialogVisible = true;
            if(this.$refs.pathSettingDialog && this.$refs.pathSettingDialog.$refs.pathSettingForm) {
                this.$refs.pathSettingDialog.$refs.pathSettingForm.resetFields();
            }
            this.toastData.btnData = [
                {
                    name:'保存',//按钮名
                    method:'saveAdd',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'保存并继续',
                    method:'savaAgain',
                    bgColor:'dialog-blue',
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }
            ];
            for(var key in this.toastData.form) {
                this.toastData.form[key] = "";
            }
        },

        /**保存新增 */
        saveAdd() {
            this.$refs.pathSettingDialog.$refs.pathSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        macNo: this.toastData.form.macNo,
                        activeNo: this.toastData.form.activeNo,
                        activeName: this.toastData.form.activeName,
                        startSiteId: this.toastData.form.startSiteId,
                        endSiteId: this.toastData.form.endSiteId,
                        offset: this.toastData.form.offset,
                        needTime: this.toastData.form.needTime,
                    };
                    postWay(obj,this,'/singlePathSet/addOrUpdate').then(data=>{
                        this.toastData.dialogVisible = false;
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
            
        },

        /**保存新增并继续 */
        savaAgain() {
            this.$refs.pathSettingDialog.$refs.pathSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        macNo: this.toastData.form.macNo,
                        activeNo: this.toastData.form.activeNo,
                        activeName: this.toastData.form.activeName,
                        startSiteId: this.toastData.form.startSiteId,
                        endSiteId: this.toastData.form.endSiteId,
                        offset: this.toastData.form.offset,
                        needTime: this.toastData.form.needTime,
                    };
                    postWay(obj,this,'/singlePathSet/addOrUpdate').then(data=>{
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        });
                        for(var key in this.toastData.form) {
                            this.toastData.form[key] = "";
                        }
                        this.$nextTick(() => {
                            this.$refs.pathSettingDialog.$refs.pathSettingForm.resetFields();
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
            
        },

        /**出现编辑弹框 */
        showEdit() {
            this.toastData.dialogVisible = true;
            this.toastData.btnData = [
                {
                    name:'保存',//按钮名
                    method:'saveEdit',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }
            ];
            for(var key in this.toastData.form) {
                this.toastData.form[key] = this.tableConfig.currentSelectArr[0][key];
            }
        },

        /**保存编辑 */
        saveEdit() {
            this.$refs.pathSettingDialog.$refs.pathSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        id: this.tableConfig.currentSelectArr[0].id,
                        macNo: this.toastData.form.macNo,
                        activeNo: this.toastData.form.activeNo,
                        activeName: this.toastData.form.activeName,
                        startSiteId: this.toastData.form.startSiteId,
                        endSiteId: this.toastData.form.endSiteId,
                        offset: this.toastData.form.offset,
                        needTime: this.toastData.form.needTime,
                    };
                    postWay(obj,this,'/singlePathSet/addOrUpdate').then(data=>{
                        this.toastData.dialogVisible = false;
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
            
        },

        /**删除 */
        remove() {
            this.$confirm("此操作将永久删除该节点设置，是否继续？", "提示", {
                confimButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                var arr = [];
                this.tableConfig.currentSelectArr.forEach(v => {
                    arr.push(v.id);
                });
                var obj = {ids: arr.join(',')};
                postWay(obj,this,'/singlePathSet/delete').then(data=>{
                    this.$message({
                        type: 'success',
                        message: '删除成功'
                    })
                    this.search();
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                })
            })
        }
    },

    watch: {
        "tableConfig.currentSelectArr": function(val) {
            if(val.length == 0) {
                this.tableConfig.toolbarConfig[1].disabled = true;
                this.tableConfig.toolbarConfig[2].disabled = true;
            }else if(val.length == 1) {
                this.tableConfig.toolbarConfig[1].disabled = false;
                this.tableConfig.toolbarConfig[2].disabled = false;
            }else if(val.length > 1) {
                this.tableConfig.toolbarConfig[1].disabled = true;
                this.tableConfig.toolbarConfig[2].disabled = false;
            }
        }
    }
}
</script>




