import {getStore} from './assets/store.js'
// import {getList} from './components/template/util/index'
export default {
  name: 'app',
  data () {
    return {

    }
  },
  props:[],
  computed:{
    // ...mapGetters(['getIsLoading'])
  },
  methods:{
    // ...mapMutations(["setUserImg", "setUserName",'setCompanyName','setCompanyEnglish','setCompanyLogo','setPorts'])
  },
  mounted(){
