<template>
  <div class="menu-wrapper">
    <template v-for="(item,index) in menu">
      <el-menu-item v-if="validatenull(item.children)"
                    v-show="!item.isHide"
                    :index="filterPath(item.href,index)"
                    @click="open(item)"
                    :key="item.label">
        <i :class="item.icon"></i>
        <span slot="title">{{item.label}}</span>
      </el-menu-item>
      <el-submenu v-else
                  :index="filterPath(item.label,index)"
                  :key="item.label">
        <template slot="title">
          <i :class="item.icon"></i>
          <span slot="title"
                :class="{'el-menu--display':isCollapse}">{{item.label}}</span>
        </template>
        <template v-for="(child,cindex) in item.children">
          <el-menu-item :class="{'siderbar-active':nowTagValue==child.href}"
                        :index="filterPath(child.href,cindex)"
                        @click="open(child)"
                        v-if="validatenull(child.children)"
                        :key="child.label">
            <i :class="child.icon"></i>
            <span slot="title">{{child.label}}</span>
          </el-menu-item>
          <sidebar-item v-else
                        :menu="[child]"
                        :key="cindex"
                        :isCollapse="isCollapse"></sidebar-item>
        </template>
      </el-submenu>
    </template>
  </div>
</template>
<script>
import { resolveUrlPath,setUrlPath} from '../../../assets/util.js'
// import  validatenull  from '../../../assets/util.js';
export default {
  name: 'SidebarItem',
  data () {
    return {}
  },
  props: {
    menu: {
      type: Array
    },
    isCollapse: {
      type: Boolean
    }
  },
  created () { },
  mounted () { },
  computed: {
    nowTagValue: function () {
      return setUrlPath(this.$route)
    }
  },
  methods: {
    filterPath (path, index) {
      return path == null ? index + '' : path
    },
    open (item) {
      this.$store.commit('ADD_TAG',{
        label: item.label,
        value: item.href,
        query: item.query,
      })
      this.$store.commit('SET_TAG_CURRENT',item. href)
      this.$router.push({
        path: resolveUrlPath(item.href, item.label),
        query: item.query
      })
    },
    validatenull(val){
      if (typeof val == 'boolean') {
        return false;
      }
      if (val instanceof Array) {
          if (val.length == 0) return true;
      } else if (val instanceof Object) {
          if (JSON.stringify(val) === '{}') return true;
      } else {
          if (val == 'null' || val == null || val == 'undefined' || val == undefined || val == '') return true;
          return false;
      }
      return false;
    }
  }
}
</script>
<style lang="less" scoped>
//刷新激活状态
.siderbar-active {
  i,
  span {
    color: #409eff;
  }
}
.el-menu--display, .el-menu--display + .el-submenu__icon-arrow {
    display: none;
}
</style>




