<template>
    <div class="logistics-control">
        <search :data="searchData" :config="searchConfig" @search="search"></search>
        <set-table :tableConfig="tableConfig" :tableData="tableData" :toolbarConfig="tableConfig.toolbarConfig" @showAdd="showAdd" @showEdit="showEdit" @remove="remove" @pageChange="pageChange" @addRoute="addRoute"></set-table>
        <!-- 设置路径集弹框 -->
        <toast :config="toastData" @cancel="toastData.dialogVisible=false" @saveAdd="saveAdd" @savaAgain="savaAgain" @saveEdit="saveEdit">
            <paths-settingDialog :form="toastData.form" :macNoList="searchConfig[0][0].childrens":optionList="toastData.optionList"  ref="pathssettingDialog"></paths-settingDialog>
        </toast>
        <!-- 添加单路径弹框 -->
        <toast :config="singleToastData" @cancelSingle="singleToastData.dialogVisible=false;" @saveAddSingle="saveAddSingle">
            <addpath-settingDialog v-if="singleToastData.flag" :routeList="singleToastData.routeList" :choseList="singleToastData.choseList" ref="addpathssettingDialog" @listChange="listChange"></addpath-settingDialog>
        </toast>
    </div>
</template>
<script>
import search from '../../components/search'
import toolbar from '../../components/toolbar'
import setTable from '../../components/table'
import toast from '../../components/toast'
import pathsSettingDialog from './pathsSettingDialog'
import addpathSettingDialog from './addpathSettingDialog'
import {postWay,getWay,postJsonWay} from './interface.js'
export default {
    data() {
        return {
            /**搜索框配置 */
            searchData: {},
            searchConfig: [
                [
                    {label: "设备编码", field: "deviceNo", type: "select", childrens: []}
                ]
            ],

            /**表格配置 */
            tableConfig: {
                toolbarConfig: [
                    {icon: "", disabled: false, method: "showAdd", name: "新增"},
                    {icon: "", disabled: true, method: "showEdit", name: "编辑"},
                    {icon: "", disabled: true, method: "remove", name: "删除"}
                ],
                height: "430",
                isHigh: false,
                isSelection: true,
                align: "center",
                isOrder: false,
                currentSelectArr: [],
                currentClickRow: {},
                colConfig: [
                    {
                        fixed: false,
                        field: "deviceNo",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "设备编码",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "startPoint",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "目标节点",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "endPoint",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "开始节点",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "pathGroup",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "路径集",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "offset",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "偏移量",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "needTime",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "需要时间",
                        type: "text",
                    },
                    {
                        fixed: false,
                        field: "",
                        width: "",
                        isSort: false,
                        isResize: "",
                        noTooltip: "操作",
                        type: "btnsText",
                        btnData: [
                            {name: '添加单路径', method: 'addRoute'}
                        ]
                    }
                ],
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0
            },

            tableData: [],

            /**弹出框配置 */
            toastData: {
                title:'设备路径集设置',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'980px',//弹窗宽
                btnData:[{
                    name:'保存',//按钮名
                    method:'saveAdd',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'保存并继续',
                    method:'savaAgain',
                    bgColor:'dialog-blue',
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }],

                form: {
                    deviceNo: '',
                    pathName: '',
                    startSiteId: '',
                    endtSiteId: '',
                    offset: '',
                    needTime: ''
                },

                mac_NoList: [],
                optionList: []
            },

            singleToastData: {
                title:'手动添加路径',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'980px',//弹窗宽
                btnData:[{
                    name:'保存',//按钮名
                    method:'saveAddSingle',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'取消',
                    method:'cancelSingle',
                    bgColor:'dialog-normal',
                    isShow: true
                }],

                routeList: [],
                choseList: [],
                singlePathId: [],
                flag: false
            },
        }
    },

    components: {
      search,
      toolbar,
      setTable,
      toast,
      pathsSettingDialog,
      addpathSettingDialog
    },

    mounted() {
        this.selectList();

        /**设备编码下拉查询 */
        getWay({},this,'/sitSet/optionDeviceNo').then(data=>{
            data.forEach(v=> {
                v.name = v.diviceNo;
                v.value = v.diviceNo;
            }) 
            this.searchConfig[0][0].childrens = data;
        })

        /**节点下拉查询 */
        getWay({},this,'/sitSet/optionSite').then(data=>{
            data.forEach(v=> {
                v.name = v.siteName;
                v.value = v.id;
            }) 
            this.toastData.optionList = data;
        })
    },

    methods: {
        /**分页查询 */
        selectList() {
            var obj = {
                pageNo: this.tableConfig.pageNo,
                pageSize: this.tableConfig.pageSize,
                deviceNo: this.searchData.deviceNo,
            };
            postWay(obj,this,'/pathCollectionsSet/page').then(data=>{
                this.tableData = data.items;
                this.tableConfig.total = data.numRows;
            })
        },

        /**按搜索条件查询 */
        search() {
            this.tableConfig.pageNo = 1;
            this.tableConfig.pageSize = 20;
            this.selectList();
        },
        
        /**分页条页码、条数改变 */
        pageChange() {
            this.selectList();
        },

        /**出现新增弹框 */
        showAdd() {
            this.toastData.dialogVisible = true;
            if(this.$refs.pathssettingDialog && this.$refs.pathssettingDialog.$refs.pathsSettingForm) {
                this.$refs.pathssettingDialog.$refs.pathsSettingForm.resetFields();
            }
            this.toastData.btnData = [
                {
                    name:'保存',//按钮名
                    method:'saveAdd',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'保存并继续',
                    method:'savaAgain',
                    bgColor:'dialog-blue',
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }
            ];
            for(var key in this.toastData.form) {
                this.toastData.form[key] = "";
            }
        },

        /**保存新增 */
        saveAdd() {
            this.$refs.pathssettingDialog.$refs.pathsSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        deviceNo: this.toastData.form.deviceNo,
                        pathName: this.toastData.form.pathName,
                        startSiteId: this.toastData.form.startSiteId,
                        endtSiteId: this.toastData.form.endtSiteId,
                        offset: this.toastData.form.offset,
                        needTime: this.toastData.form.needTime
                    };
                    postWay(obj,this,'/pathCollectionsSet/addOrUpdate').then(data=>{
                        this.toastData.dialogVisible = false;
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
            
        },

        /**保存新增并继续 */
        savaAgain() {
            this.$refs.pathssettingDialog.$refs.pathsSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        deviceNo: this.toastData.form.deviceNo,
                        pathName: this.toastData.form.pathName,
                        startSiteId: this.toastData.form.startSiteId,
                        endtSiteId: this.toastData.form.endtSiteId,
                        offset: this.toastData.form.offset,
                        needTime: this.toastData.form.needTime
                    };
                    postWay(obj,this,'/pathCollectionsSet/addOrUpdate').then(data=>{
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        });
                        for(var key in this.toastData.form) {
                            this.toastData.form[key] = "";
                        }
                        this.$nextTick(() => {
                            this.$refs.pathSettingDialog.$refs.pathSettingForm.resetFields();
                        })
                        this.search();
                    })
                }else {
                    return false;
                }
            })
        },

        /**出现编辑弹框 */
        showEdit() {
            this.toastData.dialogVisible = true;
            this.toastData.btnData = [
                {
                    name:'保存',//按钮名
                    method:'saveEdit',//回调函数
                    bgColor:'dialog-deep-blue',//按钮背景
                    isShow: true
                },{
                    name:'取消',
                    method:'cancel',
                    bgColor:'dialog-normal',
                    isShow: true
                }
            ];
            for(var key in this.toastData.form) {
                this.toastData.form[key] = this.tableConfig.currentSelectArr[0][key];
            }
        },

        /**保存编辑 */
        saveEdit() {
            this.$refs.pathssettingDialog.$refs.pathsSettingForm.validate((valid) => {
                if(valid) {
                    var obj = {
                        id: this.tableConfig.currentSelectArr[0].id,
                        deviceNo: this.toastData.form.deviceNo,
                        pathName: this.toastData.form.pathName,
                        startSiteId: this.toastData.form.startSiteId,
                        endtSiteId: this.toastData.form.endtSiteId,
                        offset: this.toastData.form.offset,
                        needTime: this.toastData.form.needTime
                    };
                    postWay(obj,this,'/pathCollectionsSet/addOrUpdate').then(data=>{
                        this.$message({
                            type: 'success',
                            message: '保存成功'
                        });
                        this.search();
                        this.toastData.dialogVisible = false;
                    })
                }else {
                    return false;
                }
            })
        },

        /**删除 */
        remove() {
            this.$confirm("此操作将永久删除该节点设置，是否继续？", "提示", {
                confimButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                var arr = [];
                this.tableConfig.currentSelectArr.forEach(v => {
                    arr.push(v.id);
                });
                var obj = {ids: arr.join(',')};
                postWay(obj,this,'/pathCollectionsSet/delete').then(data=>{
                    console.log(obj)
                    this.$message({
                        type: 'success',
                        message: '删除成功'
                    })
                    this.search();
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                })
            })
        },

        /**添加单路径 */
        addRoute(val) {
            var obj = {id: val.id}
            postWay(obj,this,'/pathCollectionsSet/beforeAddSinglePath').then(data=>{
                console.log(data)
                this.singleToastData.routeList = data.allSinglePathSets;
                this.singleToastData.choseList = data.selectedpath;
                this.singleToastData.flag = true;
            })
            this.singleToastData.dialogVisible = true;
            this.tableConfig.currentClickRow = val;
            if(this.$refs.addpathssettingDialog) {
                this.$refs.addpathssettingDialog.init();
            }
        },

        listChange(val) {
            this.singleToastData.singlePathId = val;
        },

        /**单路径保存 */
        saveAddSingle() {
            var obj = {
                pathCollectionId: this.tableConfig.currentClickRow.id,
                singlePathId: this.singleToastData.singlePathId
            };
            postJsonWay(obj,this,'/pathCollectionsSet/saveRelations').then(data=>{
                this.toastData.dialogVisible = false;
                this.$message({
                    type: 'success',
                    message: '保存成功'
                })
                this.search();
                this.singleToastData.dialogVisible = false;
            })
        },
    },

    watch: {
        "tableConfig.currentSelectArr": function(val) {
            if(val.length == 0) {
                this.tableConfig.toolbarConfig[1].disabled = true;
                this.tableConfig.toolbarConfig[2].disabled = true;
            }else if(val.length == 1) {
                this.tableConfig.toolbarConfig[1].disabled = false;
                this.tableConfig.toolbarConfig[2].disabled = false;
            }else if(val.length > 1) {
                this.tableConfig.toolbarConfig[1].disabled = true;
                this.tableConfig.toolbarConfig[2].disabled = false;
            }
        },

        "singleToastData.dialogVisible": function(val) {
            if(!val) {
                this.singleToastData.flag = false;
            }
        }
    }
}
</script>




