<template>
    <div class="pathSettingDialog">
        <el-form :model="form" :rules="rules" label-width="120px" ref="equipmentSettingForm" :form="form" :macNoList="macNoList" :siteTypeList="siteTypeList">
            <el-row>
                <el-col :span="8">
                    <el-form-item label="设备编码：" prop="diviceNo">
                        {{form.diviceNo}}
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="机联网编码：" prop="catNo">
                         {{form.catNo}}
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="品牌：" prop="brand">
                        {{form.brand}}
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8">
                    <el-form-item label="出厂年月：" prop="ym">
                        {{form.ym}}
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="生产厂家：" prop="factory">
                        {{form.factory}}
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="库区：" prop="storeAreaId">
                        {{form.storeAreaId}}
                    </el-form-item>
                </el-col>
            </el-row>
            <!-- <el-row>
                <el-col :span="8">
                    <el-form-item label="max-x：" prop="maxX">
                        {{form.maxX}}
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="max-y：" prop="maxY">
                        {{form.maxY}}
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="max-z：" prop="maxZ">
                        {{form.maxZ}}
                    </el-form-item>
                </el-col>
            </el-row> -->
        </el-form>
    </div>
</template>
<script>
export default {
    props: ["form", "macNoList", "siteTypeList"],

    data() {
        return {
            rules: {
                mac_no: [
                    { required: true, message: '请选择设备编码', trigger: 'change' }
                ]
            }
        }
    }
}
</script>
<style lang="less">
    .pathSettingDialog {

    }
</style>



