<template>
    <div class="debug">
        <div class="control-img no-scroll">
            <div class="control-header">
                <div>
                    <span class="select-label">最多显示</span>
                    <el-select v-model="maxShowValue" placeholder="请选择" size="small" style="width:120px">
                        <el-option :label="1"  :value="1"></el-option>
                        <el-option :label="3"  :value="3"></el-option>
                        <el-option :label="5"  :value="5"></el-option>
                        <el-option label="全部"  value="all"></el-option>
                    </el-select>
                </div>
                <div style="margin-top:15px;">
                    <span class="select-label">小车经过时</span>
                    <img src="../../assets/image/busyBox.png" alt="">
                    <img src="../../assets/image/busyFullBox.png" alt="">
                    <span class="select-label other">满箱状态</span>
                    <img src="../../assets/image/full.png" alt="">
                     <span class="select-label other">空箱状态</span>
                    <img src="../../assets/image/empty.png" alt="">
                </div>
            </div>
            <div class="control-content">
                <div>
                    <span class="select-label">设备编码00001</span>
                    <span class="select-label other">紧急启停</span>
                    <el-switch
                        v-model="isStop"
                        active-color="#3B99FC"
                        inactive-color="#ff4949">
                    </el-switch>
                    <span class="select-label other localtion">实时位置节点：x=9,y=0,z=1</span>
                    <span class="select-label other">当前显示层</span>
                    <el-select v-model="maxShowValue" placeholder="请选择" size="small" style="width:120px">
                        <el-option :label="1"  :value="1"></el-option>
                        <el-option :label="3"  :value="3"></el-option>
                        <el-option :label="5"  :value="5"></el-option>
                        <el-option label="全部"  value="all"></el-option>
                    </el-select>
                </div>
                <div class="control-main">
                    <div class="main-top">
                        <span class="cell" :class="{'firstCell':index==0}" v-for="(o,index) in 22"></span>
                    </div>
                    <div class="main-bottom">
                        <span class="cell" :class="{'firstCell':index==0}"  v-for="(o,index) in 12"></span>
                    </div>
                    <div class="car">
                    </div>
                </div>
                <p class="select-label" style="margin-top:40px">设备编码</p>
                <div class="carBox">
                    <ul class="clearfix">
                        <li v-for="(car,index) in cars" :key="index">
                            <img src="../../assets/image/emptyCar.png" alt="">
                            <p>{{car.code}}</p>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="control-footer">
                <div>
                    <span class="select-label">最多显示</span>
                    <el-select v-model="maxShowValue" placeholder="请选择" size="small" style="width:120px">
                        <el-option :label="1"  :value="1"></el-option>
                        <el-option :label="3"  :value="3"></el-option>
                        <el-option :label="5"  :value="5"></el-option>
                        <el-option label="全部"  value="all"></el-option>
                    </el-select>
                </div>
                <div>
                    <div class="data-tabs">
                        <span class="data-tab" :class="{'active':currentTab==data.value}" v-for="(data,index) in datas" :key="index" @click="tabClick(data.value)">{{data.name}}</span>
                    </div>
                    <el-table
                        :data="tableData"
                        header-row-class-name="table-header"
                        max-height="192px"
                        style="width: 100%">
                        <el-table-column
                            prop="date"
                            label="机器猫编码"
                            show-overflow-tooltip
                            align="center"
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="name"
                            align="center"
                            label="设备编码"
                            show-overflow-tooltip
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="address"
                            align="center"
                            show-overflow-tooltip
                            label="通讯键">
                        </el-table-column>
                        <el-table-column
                            prop="address"
                            align="center"
                            show-overflow-tooltip
                            label="键值">
                        </el-table-column>
                        <el-table-column
                            prop="address"
                            align="center"
                            show-overflow-tooltip
                            label="时间戳">
                        </el-table-column>
                    </el-table>
                </div>
            </div>
        </div>
        <div class="table-box">
            <div class="table-box-center">
                <el-row :gutter="30">
                    <el-col :span="8">
                        <tool>
                            <span slot="left">任务A路径信息</span>
                        </tool>
                        <el-table
                            :data="message1"
                            header-row-class-name="table-header"
                            max-height="192px"
                            style="width: 100%">
                            <el-table-column
                                prop="name"
                                header-align="center"
                                label="姓名"
                                show-overflow-tooltip>
                            </el-table-column>
                        </el-table>
                    </el-col>
                    <el-col :span="8">
                        <tool>
                            <span slot="left">任务A路径集信息</span>
                        </tool>
                        <el-table
                            :data="message2"
                            header-row-class-name="table-header"
                            max-height="192px"
                            style="width: 100%">
                            <el-table-column
                                prop="name"
                                header-align="center"
                                label="姓名"
                                show-overflow-tooltip>
                            </el-table-column>
                        </el-table>
                    </el-col>
                    <el-col :span="8">
                        <tool>
                            <span slot="left">任务A动作信息</span>
                        </tool>
                        <el-table
                            :data="message2"
                            header-row-class-name="table-header"
                            max-height="192px"
                            style="width: 100%">
                            <el-table-column
                                prop="name"
                                header-align="center"
                                label="姓名"
                                show-overflow-tooltip>
                            </el-table-column>
                        </el-table>
                    </el-col>
                </el-row>
            </div>
            <div class="table-box-bottom">
                <tool>
                    <span slot="left">历史记录</span>
                </tool>
                <el-table
                    :data="message2"
                    header-row-class-name="table-header"
                    max-height="192px"
                    style="width: 100%">
                    <el-table-column
                        prop="name"
                        header-align="center"
                        label="姓名"
                        show-overflow-tooltip>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            currentTab:1,
            datas:[{name:'机联网上行数据',value:1},{name:'机联网下行数据',value:2},{name:'机联网告警记录',value:3}],
            cars:[{code:'1111'},{code:'1234'},{code:'2234'}],
            maxShowValue:'all',
            //紧急启停
            isStop:false,
            tasks:[{
                name:'物流任务1',
                id:0
            },{
                name:'物流任务2',
                id:1
            },{
                name:'物流任务3',
                id:3
            }],
            message1:[],
            message2:[],
            message3:[],
            tableData: [{
                date: '2016-05-02',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }, {
                date: '2016-05-04',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1517 弄'
            }, {
                date: '2016-05-01',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1519 弄'
            }],
            type:[]
        }
    },
    methods: {
        tabClick(tab){
            this.currentTab=tab
        }
    }
}
</script>

<style lang="less">
    .debug{
        // height: 820px;
        position: relative;
        .table-box{
            height: 100%;
            margin-left: 0;
            margin-right: 820px;
            box-sizing: border-box;
            padding:0 20px;
            .table-box-center{
                height: 200px;
                overflow: hidden;
            }
            .table-box-bottom{
                margin-top: 36px;
                height: 200px;
                overflow: hidden;
            }
        }
        .control-img{
            border-left: 1px solid rgba(229,229,229,1);
            box-sizing: border-box;
            width: 820px;
            height: 100%;
            overflow: auto;
            float: right;
            .control-header,.control-content{
                padding: 24px;
                box-sizing: border-box;
                border-bottom: 1px dashed  rgba(229,229,229,1);
                .select-label{
                    margin-right: 10px;
                    font-size:14px;
                    font-family:MicrosoftYaHei;
                    font-weight:400;
                    color:rgba(102,102,102,1);
                    display: inline-block;
                    height: 32px;
                    line-height: 32px;
                    transform: translateY(2px);
                    &.other{
                        margin-left: 20px;
                    }
                    &.localtion{
                        font-size:14px;
                        font-family:MicrosoftYaHei;
                        font-weight:400;
                        color:rgba(59,153,252,1);
                    }
                }
                .carBox{
                    ul{
                        li{
                            float: left;
                            margin-right: 20px;
                            p{
                                font-size:12px;
                                font-family:MicrosoftYaHei;
                                font-weight:400;
                                color:rgba(102,102,102,1);
                                text-align: center;
                            }
                        }
                    }
                }
            }
            .control-main{
                margin-top: 15px;
                min-width: 100%;
                position: relative;
                padding-bottom: 20px;
                overflow: auto;
                .main-top{
                    height: 72px;
                    display: inline-block;
                    white-space: nowrap;
                    border-bottom: 1px solid rgba(205,205,205,1);
                    border-left:  1px solid rgba(205,205,205,1);
                }
                .main-bottom{
                    display: inline-block;
                    height: 72px;
                    white-space: nowrap;
                    border-left:  1px solid rgba(205,205,205,1);
                }
                .main-top,.main-bottom{
                    .cell{
                        width: 50px;
                        height: 50px;
                        // float: left;
                        display: inline-block;
                        background: url('../../assets/image/empty.png') center no-repeat;
                        // margin-bottom: 11px;
                        border:1px dashed rgba(205,205,205,1);
                        border-left: none;
                        &.firstCell{
                            margin-left: 10px;
                            border-left:1px dashed rgba(205,205,205,1);
                        }
                    }
                }
                .main-top .cell{
                     margin-bottom: 20px;
                }
                .main-bottom {
                    transform: translateY(-2px);
                }
                .main-bottom .cell{
                     margin-top: 20px;
                }
                .car{
                    position:absolute;
                    left: 0;
                    width: 50px;
                    top: 48px;
                    height: 50px;
                    background: url(../../assets/image/emptyCar.png) center no-repeat;
                }
            }
            .control-footer{
                padding: 24px;
                box-sizing: border-box;
                .select-label{
                    margin-right: 10px;
                    font-size:14px;
                    font-family:MicrosoftYaHei;
                    font-weight:400;
                    color:rgba(102,102,102,1);
                    display: inline-block;
                    height: 32px;
                    line-height: 32px;
                    transform: translateY(2px);
                    &.other{
                        margin-left: 20px;
                    }
                    &.localtion{
                        font-size:14px;
                        font-family:MicrosoftYaHei;
                        font-weight:400;
                        color:rgba(59,153,252,1);
                    }
                }
                .data-tabs{
                    margin:30px 0;
                    .data-tab{
                        font-size:14px;
                        font-family:MicrosoftYaHei;
                        font-weight:400;
                        color:rgba(161,161,161,1);
                        margin-right: 20px;
                        cursor: pointer;
                        // color:rgba(21,142,252,1);
                        &.active{
                            color:rgba(21,142,252,1);
                        }
                    }
                }
            }
        }
    }
</style>




