<template>
    <div class="task">
        <div class="tabs">
            <ul>
                <li :class="{'is-active':current==item}" v-for="item in tabs" :key="item" @click="current=item">{{item}}</li>
            </ul>
        </div>
        <div class="content">
            <transition name="fade-transverse">
                <material v-if="current=='上下料请求'"></material>
                <move  v-if="current=='移箱请求'"></move>
            </transition>
        </div>
    </div>
</template>
<script>
import material from './material'
import move from './move'
export default {
    data(){
        return{
            current:'上下料请求',
            tabs:['上下料请求','移箱请求'],
            websocket:null
        }
    },
    components:{
        material,
        move
    },
    mounted(){
        this.websocket = new WebSocket(window.sessionStorage.socketUrl);
        this.websocket.onmessage=(res)=>{
            console.log(res.data)
        }
        this.websocket.onerror=(err)=>{
            console.log(err)
        }
    },
    destroyed(){
        if(this.websocket)
            this.websocket.onclose
    }
}
</script>
<style lang="less" >
    .fade-transverse-leave-active,
    .fade-transverse-enter-active {
        transition: all .5s;
    }

    .fade-transverse-enter {
        opacity: 0;
        transform: translateX(-30px);
    }

    .fade-transverse-leave-to {
        opacity: 0;
        transform: translateX(30px);
    }
    .task{
        display: flex;
        flex-direction: column;
        padding: 0px 20px;
        box-sizing: border-box;
        // background: #fff;
        height: 100%;
        width: 100%;
        overflow: hidden;
        .tabs{
            width: 100%;
            border-bottom: 1px solid rgba(229,229,229,1);
            height: 50px;
            ul{
                &::after{
                    content: '';
                    display: block;
                    clear: both;
                }
                li{
                    float: left;
                    padding: 10px 40px 5px;
                    font-size:18px;
                    font-family:MicrosoftYaHei;
                    font-weight:400;
                    color:rgba(51,51,51,1);
                    line-height:36px;
                    position: relative;
                    cursor: pointer;
                    &.is-active::after{
                        content: '';
                        display:block;
                        width: 36px;
                        height: 2px;
                        position: absolute;
                        left: 50%;
                        bottom: 0px;
                        background: #2A93FF;
                        transform: translate(-50%,0);
                        animation: show .2s ease-in-out;
                    }
                }
            }
        }
        .content{
            flex:1;
            overflow: auto;
        }
    }
    @keyframes show {
        from{
            width:0px;
        }
        to{
            width: 36px;
        }
    }
</style>




