<template>
    <div class="material">
        <tool>
            <span slot="left">
                完成状态：
                <el-select v-model="status"  size="small">
                  <el-option label="未完成" value="未完成"></el-option>
                  <el-option label="已完成" value="已完成"></el-option>
                  <el-option label="暂停" value="暂停"></el-option>
                </el-select>
            </span>
            <span slot="right">
                <el-button type="primary" icon="el-icon-search" size="small" @click="search">搜索</el-button>
            </span>
        </tool>
        <div>
            <el-table
                :data="tableData"
                highlight-current-row
                @current-change="handleCurrentChange"
                height="300"
                header-row-class-name="table-header"
                style="width: 100%">
                <el-table-column
                prop="taskId"
                label="任务号"
                show-overflow-tooltip
                width="180">
                </el-table-column>
                <el-table-column
                prop="storeName"
                label="仓库名"
                show-overflow-tooltip
                width="180">
                </el-table-column>
                <el-table-column
                prop="areaName"
                label="库区"
                show-overflow-tooltip
                width="180">
                </el-table-column>
                <el-table-column
                prop="startPoint"
                show-overflow-tooltip
                label="请求节点">
                </el-table-column>
                <el-table-column
                prop="endPoint"
                show-overflow-tooltip
                label="目标节点">
                </el-table-column>
                <el-table-column
                prop="matterType"
                show-overflow-tooltip
                label="物料类型">
                </el-table-column>
                <el-table-column
                prop="matterName"
                show-overflow-tooltip
                label="物料信息">
                </el-table-column>
                <el-table-column
                prop="inMark"
                show-overflow-tooltip
                label="进出标记">
                </el-table-column>
                <el-table-column
                prop="requestTime"
                show-overflow-tooltip
                label="请求时间">
                </el-table-column>
                <el-table-column
                prop="isFinished"
                show-overflow-tooltip
                label="完成状态">
                </el-table-column>
            </el-table>
        </div>
        <transition name="el-fade-in-linear">
            <div class="second-table" v-if="currentRow.taskId>0">
                <tool>
                    <span slot="right">
                        <el-button type="primary" icon="el-icon-plus" size="small" @click="add">新增</el-button>
                        <el-button  icon="el-icon-edit" size="small" :disabled="selectData.length!=1" @click="edit">编辑</el-button>
                        <el-button  icon="el-icon-delete" size="small" :disabled="selectData.length<=0" @click="del">删除</el-button>
                        <el-button  icon="el-icon-delete" size="small" @click="start">启动</el-button>
                    </span>
                </tool>
                <el-table
                    :data="tableData2"
                    header-row-class-name="table-header"
                    @select-all="select"
                    @select="select"
                    style="width: 100%">
                    <el-table-column
                        type="selection"
                        width="55">
                    </el-table-column>
                    <el-table-column
                    prop="taskId"
                    label="任务号"
                    show-overflow-tooltip
                    width="180">
                    </el-table-column>
                    <el-table-column
                    prop="startSiteName"
                    show-overflow-tooltip
                    label="请求节点"
                    width="180">
                    </el-table-column>
                    <el-table-column
                    prop="endSiteName"
                    show-overflow-tooltip
                    label="目标节点">
                    </el-table-column>
                    <el-table-column
                    prop="requestedTime"
                    show-overflow-tooltip
                    label="需完成时间">
                    </el-table-column>
                    <el-table-column
                    prop="jobStatus"
                    show-overflow-tooltip
                    label="完成状态">
                    </el-table-column>
                </el-table>
            </div>
        </transition>
        <toast :config="toastConfig" @submit="submit" @cancel="cancel" @handleClose="cancel" @savaAgain="savaAgain">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px"size="small">
                <el-form-item label="请求节点" prop="startSiteId">
                    <el-select v-model="ruleForm.startSiteId"  style="width:100%" filterable >
                        <el-option v-for="(item,index) in list" :label="item.siteName" :value="item.id" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="目标节点" prop="endSiteId">
                    <el-select v-model="ruleForm.endSiteId"  style="width:100%">
                        <el-option v-for="(item,index) in list" :label="item.siteName" :value="item.id" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="需完成时间" prop="requestedTime">
                    <el-date-picker type="datetime" placeholder="选择日期"  value-format="yyyy-MM-dd HH:mm:ss" v-model="ruleForm.requestedTime" style="width: 100%;"></el-date-picker>
                </el-form-item>
            </el-form>
        </toast>
    </div>
</template>
<script>
import {upDownGet,upDown,optionSite,upDownAdd,update,del,startMission} from './api.js'
export default {
    data(){
        return{
            selectData:[],
            status:'未完成',
            list:[],
            tableData:[],
            tableData2:[],
            currentRow:{
                taskId:-1
            },
            rules:{
                startSiteId:[{ required: true,type:'number', message: '请输入请求节点', trigger: 'change' }],
                endSiteId:[{ required: true,type:'number', message: '请输入目标节点', trigger: 'change' }],
                requestedTime:[{ required: true, message: '请输入需完成时间', trigger: 'blur' }],
            },
            ruleForm:{},
            toastConfig:{
                title:'新增',//弹窗标题
                dialogVisible:false,//弹窗显示
                width:'600px',//弹窗宽
                btnData:[{
                    name:'确定',//按钮名
                    method:'submit',//回调函数
                    type:'primary',
                    isShow:true
                },{
                    name:'确定并继续',
                    method:'savaAgain',
                    type:'primary',
                    isShow:true
                },{
                    name:'取消',
                    method:'cancel',
                    isShow:true
                }]
            }
        }
    },
    methods:{
        start(){
            // 
            startMission({taskId:this.currentRow.taskId}).then(res=>{
                this.$message.success('启动成功')
            })
        },
        select(data){
            this.selectData=data
        },
        search(){
            this.currentRow={taskId:-1}
            upDownGet({isFinished:this.status}).then(res=>{
                this.tableData=res
            })
        },
        handleCurrentChange(val){
            this.currentRow=val
            if(val){
                this.getTableData()
            }
        },
        getTableData(){
            upDown({taskId:this.currentRow.taskId}).then(res=>{
                this.tableData2=res
            })
        },
        add(){
            this.toastConfig.btnData[1].isShow=true
            this.toastConfig.title='新增'
            this.ruleForm={
                taskId:this.currentRow.taskId
            }
            this.toastConfig.dialogVisible=true
        },
        edit(){
           this.toastConfig.btnData[1].isShow=false
            this.toastConfig.title='编辑'
            this.ruleForm={
                ...this.selectData[0]
            }
            this.toastConfig.dialogVisible=true
        },
        submit(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    if(this.toastConfig.title=='新增'){
                        upDownAdd({...this.ruleForm}).then(res=>{
                            this.getTableData()
                        })
                        this.toastConfig.dialogVisible=false;
                        this.$refs['ruleForm'].resetFields()
                    }else{
                        update({...this.ruleForm}).then(res=>{
                            this.selectData=[]
                            this.getTableData()
                            this.toastConfig.dialogVisible=false;
                        this.$refs['ruleForm'].resetFields()
                        })
                    }
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        savaAgain(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    upDownAdd({...this.ruleForm}).then(res=>{
                        this.ruleForm={
                            taskId:this.currentRow.taskId
                        }
                        this.getTableData()
                        this.$refs['ruleForm'].resetFields()
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        del(){
            this.$confirm('此操作将永久删除该任务, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                var ids=[]
                this.selectData.forEach(val=>{
                    ids.push(val.id)
                })
                del({ids:ids.join(',')}).then(res=>{
                    this.getTableData()
                    this.selectData=[]
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        },
        cancel(){
            this.$refs['ruleForm'].clearValidate()
             this.toastConfig.dialogVisible=false;
        }
    },
    mounted(){
        optionSite({}).then(res=>{
            this.list=res
        })
    }
}
</script>
<style lang="less">
    .material{
        .second-table{
            margin-top:50px;
            padding-top:20px;
            border-top:1px solid #eee;
        }
    }
</style>



